import { spanChartColors } from '../utils/constants';
import { getURLNameFromRealName } from '../utils/textUtils';
const d3 = require('d3');

export class d3SpanChart {
  constructor(svg, data, margin, parent) {
    this.data = data;
    this.svg = svg;
    this.margin = margin;
    this.parent = parent;
    this.container = d3.select('.span-chart-container');

    const containerHeight = this.container.style('height');
    this.svg.style('height', containerHeight);

    this.width =
      Math.round(+svg.style('width').replace('px', '')) -
      this.margin.left -
      this.margin.right;
    this.height =
      Math.round(+svg.style('height').replace('px', '')) -
      this.margin.top -
      this.margin.bottom;

    this.xScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([0, this.width]);
    this.yScale = d3
      .scaleBand()
      .domain(this.data[0].values.map(el => el.key))
      .range([0, this.height])
      .padding(0.2);
    this.colorScale = d3.scaleOrdinal(d3.schemeSet1);

    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale);

    this.g = this.svg
      .append('g')
      .attr('transform', 'translate(' + margin.left + ', ' + margin.top + ')');
  }

  createScales(countries) {
    this.yScale = d3
      .scaleBand()
      .domain(countries)
      .range([0, this.height])
      .padding(0.2);

    this.xScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([0, this.width]);

    this.g
      .selectAll('.vertical-grid-line')
      .data([25, 50, 75, 100])
      .enter()
      .append('line')
      .attr('class', 'vertical-grid-line grid-line')
      .attr('x1', d => this.xScale(d))
      .attr('y1', this.height)
      .attr('x2', d => this.xScale(d))
      .attr('y2', 0)
      .attr('stroke', 'gray')
      .attr('stroke-dasharray', '2,2');
  }

  resize(pixWidth, pixHeight) {
    const containerHeight = this.container.style('height');
    this.svg.style('height', containerHeight);

    this.width =
      Math.round(+pixWidth.replace('px', '')) -
      this.margin.left -
      this.margin.right;
    this.height =
      Math.round(+pixHeight.replace('px', '')) -
      this.margin.top -
      this.margin.bottom;
    this.xScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([0, this.width]);
    this.yScale = d3
      .scaleBand()
      .domain(this.data[0].values.map(el => el.key))
      .range([0, this.height])
      .padding(0.2);
    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale);
  }

  drawAxis() {
    this.xAxis = d3.axisBottom(this.xScale).tickValues([0, 25, 50, 75, 100]);

    var x = this.g.selectAll('.span-xaxis').data(['dummy']);
    x.enter()
      .append('g')
      .merge(x)
      .call(this.xAxis)
      .attr('class', 'span-xaxis')
      .attr('transform', 'translate(' + 0 + ', ' + this.height + ')');

    this.yAxis = d3.axisLeft(this.yScale);

    var y = this.g.selectAll('.span-yaxis').data(['dummy']);

    y.enter()
      .append('g')
      .merge(y)
      .call(this.yAxis)
      .attr('class', 'span-yaxis');

    this.g.selectAll('.vertical-grid-line').remove();
    this.g
      .selectAll('.vertical-grid-line')
      .data([25, 50, 75, 100])
      .enter()
      .append('line')
      .attr('class', 'vertical-grid-line grid-line')
      .attr('x1', d => this.xScale(d))
      .attr('y1', this.height)
      .attr('x2', d => this.xScale(d))
      .attr('y2', 0)
      .attr('stroke', '#DCDCDC');
  }

  render(props) {
    const YEAR = process.env.REACT_APP_YEAR;

    const {
      metrics,
      activeMetric,
      selectedCountries,
      metricIsSelected,
      hovered,
      toResize,
      sortOrder,
      width,
      height,
    } = props;

    var accumulatedDataArray = [];
    var yearData = this.data.find(el => el.key === YEAR);

    // create ordering always based off mean_overall metric
    var overallData = yearData.values
      .map(el => {
        return el.values[0];
      })
      .map(el => {
        const retVal = {
          country: el.country,
          mean_inputs: +el['mean_inputs'],
          mean_throughputs: +el['mean_throughputs'],
          mean_outputs: +el['mean_outputs'],
          mean_overall: +el['mean_overall'],
        };
        return retVal;
      });

    //sorts by overall value
    overallData = overallData.sort((a, b) => b[sortOrder] - a[sortOrder]);
    const sortedCountries = overallData.map(el => el.country);

    // create scales based of countries included and draw axis
    if (width && height && toResize) {
      this.resize(width, height);
    }

    this.createScales(sortedCountries);
    this.drawAxis();

    // draw horizontal lines
    var spanLines = this.g.selectAll('.span-line').data(overallData);
    spanLines
      .enter()
      .append('line')
      .merge(spanLines)
      .attr('class', 'span-line')
      .attr('x1', 0)
      .attr('y1', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('x2', this.width)
      .attr('y2', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('fill', 'none')
      .attr('stroke', '#DCDCDC');

    // create dataset from only provided metrics
    var allMetricsData = yearData.values.map(el => {
      return el.values[0];
    });

    metrics.forEach(metric => {
      var singleMetricData = allMetricsData.map(el => {
        const retVal = {
          country: el.country,
          value: +el[metric],
          metric: metric,
        };
        return retVal;
      });
      accumulatedDataArray.push(...singleMetricData);
    });

    // define event functions that use both the element and greater class
    const isolateAndRadar = dotThis => {
      const element = d3.select(dotThis);
      const data = element.data()[0];

      this.parent.dotHoverHandler(data.country, data.metric);
    };

    const resetToNormalColors = dotThis => {
      this.parent.dotResetHover();
    };

    const dotClicked = dotThis => {
      const element = d3.select(dotThis);
      const data = element.data()[0];

      this.parent.dotClickedHandler(data.country, data.metric);
    };

    // draw markers for datapoints
    var markers = this.g.selectAll('.marker').data(accumulatedDataArray);
    markers
      .enter()
      .append('circle')
      .merge(markers)
      .attr('class', d => `marker marker-${d.metric}`)
      .attr('cx', d => this.xScale(d.value))
      // .attr('cx', d => this.xScale(0))
      .attr('cy', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('r', 5)
      .attr('fill', d => {
        if (activeMetric) {
          if (d.metric === activeMetric) {
            return spanChartColors[d.metric];
          } else {
            return spanChartColors.grayColor;
          }
        } else {
          return spanChartColors[d.metric];
        }
      })
      .on('mouseover', function() {
        isolateAndRadar(this);
      })
      .on('mouseout', function() {
        resetToNormalColors(this);
      })
      .on('click', function() {
        dotClicked(this);
      });

    d3.selectAll('.selection-marker').remove();
    // draw circles over selected data points to indicate highlight

    var hoverCircles = this.g
      .selectAll('.hover-circle')
      .data(accumulatedDataArray);
    hoverCircles
      .enter()
      .append('circle')
      .merge(hoverCircles)
      .attr(
        'class',
        d =>
          `hover-circle hover-circle-${getURLNameFromRealName(d.country)}-${
            d.metric
          }`
      )
      .attr('cx', d => this.xScale(d.value))
      .attr('cy', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('r', 10)
      .attr('stroke', d => spanChartColors[d.metric])
      .attr('fill', 'none')
      .attr('opacity', 0);

    if (metricIsSelected) {
      var selectionMarkers = this.g
        .selectAll('.selection-marker')
        .data(accumulatedDataArray);
      selectionMarkers
        .enter()
        .append('circle')
        .merge(selectionMarkers)
        .filter(
          d =>
            d.metric === activeMetric && selectedCountries.includes(d.country)
        )
        .attr('class', 'selection-marker')
        .attr('cx', d => this.xScale(d.value))
        .attr('cy', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
        .attr('r', 10)
        .attr('stroke', d => spanChartColors[d.metric])
        .attr('fill', 'none');

      selectionMarkers
        .enter()
        .append('line')
        .merge(selectionMarkers)
        .filter(
          d =>
            d.metric === activeMetric && selectedCountries.includes(d.country)
        )
        .attr('class', 'selection-marker')
        .attr('x1', 0)
        .attr('y1', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
        .attr('x2', this.width)
        .attr('y2', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
        .attr('stroke', d => spanChartColors[d.metric])
        .attr('fill', 'none');

      d3.selectAll(`.marker-${activeMetric}`).raise();
    }
  }
}
