import { radarChartColors } from '../utils/constants'
import { spanChartColors } from '../utils/constants'
import { getIndexNameFromId, assembleIndexTooltip } from '../utils/textUtils'
import d3Tip from 'd3-tip'
const d3 = require('d3')

const PI = Math.PI
const TWO_PI = Math.PI * 2

export class d3SideRadarChart {
  constructor(svg, data, margin, parent, isSideRadar) {
    this.data = data
    this.svg = svg
    this.margin = margin
    this.parent = parent
    this.isSideRadar = isSideRadar

    this.svgName = this.svg.node().getAttribute('id')

    this.width =
      Math.round(+svg.style('width').replace('px', '')) -
      this.margin.left -
      this.margin.right
    let newHeight
    if (isSideRadar) {
      const containerHeight = d3
        .select('.radar-side-bar')
        .style('height')
        .replace('px', '')
      const halfContainerHeight = containerHeight / 1.6
      newHeight = Math.round(Math.min(halfContainerHeight, this.width)) + 'px'
      this.svg.style('height', newHeight)
      this.svg.select('.radar-chart-container').style('height', newHeight)
      this.height =
        Math.round(+this.svg.style('height').replace('px', '')) -
        this.margin.top -
        this.margin.bottom
      const smallestDimension = Math.min(this.width, this.height)
      this.chartR = smallestDimension / 3
    } else {
      this.svg.select('.radar-chart-container').style('height', '100%')
      this.height =
        Math.round(+svg.style('height').replace('px', '')) -
        this.margin.top -
        this.margin.bottom
      const smallestDimension = Math.min(this.width, this.height)
      this.chartR = smallestDimension / 3
    }

    this.linearScale = d3.scaleLinear().domain([0, 100]).range([0, this.chartR])

    this.thetaOffset = PI / 4

    this.centerX = this.width / 2 + 15
    this.centerY = this.height / 2

    //dn@berggruen.org

    this.g = this.svg.append('g').attr('class', 'radar-group')

    d3.selectAll(
      `.radar-tooltip-${this.svgName}, .index-tooltip-${this.svgName}`
    ).remove()
    // tooltip setup
    d3.tip = d3Tip
    this.tip = d3
      .tip()
      .attr('class', `radar-tooltip radar-tooltip-${this.svgName}`)
      .html((d) => `${d}<span class='tooltip-gray-part'>/100</span>`)
    this.tip.offset([-10, 0])
    this.svg.call(this.tip)

    this.indexTip = d3
      .tip()
      .attr('class', `index-tooltip index-tooltip-${this.svgName}`)
      // .html(d => `${d}`)
      .html((d) => assembleIndexTooltip(d))
    this.indexTip.offset([-10, 0])
    this.indexTip.direction('n')
    this.svg.call(this.indexTip)
  }

  resize(pixWidth, pixHeight) {
    this.width =
      Math.round(+this.svg.style('width').replace('px', '')) -
      this.margin.left -
      this.margin.right
    let newHeight
    if (this.isSideRadar) {
      const containerHeight = d3
        .select('.radar-side-bar')
        .style('height')
        .replace('px', '')
      const halfContainerHeight = containerHeight / 1.6
      newHeight = Math.round(Math.min(halfContainerHeight, this.width)) + 'px'
      this.svg.style('height', newHeight)
      this.svg.select('.radar-chart-container').style('height', newHeight)
      this.height =
        Math.round(+this.svg.style('height').replace('px', '')) -
        this.margin.top -
        this.margin.bottom
      const smallestDimension = Math.min(this.width, this.height)
      this.chartR = smallestDimension / 3
    } else {
      this.svg.select('.radar-chart-container').style('height', newHeight)
      this.height =
        Math.round(+this.svg.style('height').replace('px', '')) -
        this.margin.top -
        this.margin.bottom
      const smallestDimension = Math.min(this.width, this.height)
      this.chartR = smallestDimension / 3
    }

    this.linearScale = d3.scaleLinear().domain([0, 100]).range([0, this.chartR])

    this.thetaOffset = PI / 4

    this.centerX = this.width / 2 + 15
    this.centerY = this.height / 2
  }

  renderSpokes(metric) {
    this.svg
      .selectAll(
        '.radar-points, .radar-polygon, .radar-spokes, .radar-spoke-labels, .radar-container'
      )
      .remove()

    this.g.selectAll('.radar-container').remove()
    ;[1, 0.75, 0.5, 0.25].forEach((band) => {
      this.g
        .append('circle')
        .attr('class', 'radar-container')
        .attr(
          'transform',
          'translate(' + this.centerX + ', ' + this.centerY + ')'
        )
        .attr('cx', 0)
        .attr('cy', 0)
        .attr('r', this.chartR * band)
        .attr('fill', 'none')
        .attr('stroke', radarChartColors.grayColor)
    })

    this.svg.selectAll('.radar-spokes,.radar-spoke-labels').remove()

    var radarColumns
    var metricName
    if (metric === 'mean_overall') {
      metricName = 'Berggruen Index'
      radarColumns = ['mean_inputs', 'mean_throughputs', 'mean_outputs']
    } else if (metric === 'mean_inputs') {
      metricName = 'Democracy'
      radarColumns = [
        'mean_civ.soc',
        'mean_culture',
        'mean_feedback',
        'mean_democratic.engagement',
      ]
    } else if (metric === 'mean_throughputs') {
      metricName = 'Government'
      radarColumns = [
        'mean_analytical',
        'mean_coordination',
        'mean_corruption',
        'mean_impartiality',
        'mean_judicial.impartiality',
        'mean_politicized.bureaucracy',
        'mean_procedural.efficiency',
        'mean_recruitment',
        'mean_regulatory.burden',
        'mean_tax.collection',
        'mean_government.transparency',
      ]
    } else if (metric === 'mean_outputs') {
      metricName = 'Life'
      radarColumns = [
        'mean_civil.justice',
        'mean_crime',
        'mean_business.regulation',
        'mean_other.public.services',
        'mean_education',
        'mean_environment',
        'mean_inflation',
        'mean_price.levels',
        'mean_public.health',
        'mean_shadow.economy',
      ]
    }

    for (let i = 0; i < radarColumns.length; i++) {
      const el = radarColumns[i]
      const arcSize = TWO_PI / radarColumns.length
      const theta = i * arcSize

      this.g
        .append('line')
        .attr('class', 'radar-spokes')
        .attr(
          'transform',
          'translate(' + this.centerX + ', ' + this.centerY + ')'
        )
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', Math.cos(this.thetaOffset + theta) * this.chartR)
        .attr('y2', Math.sin(this.thetaOffset + theta) * this.chartR)
        .attr('stroke', radarChartColors.grayColor)
        .attr('strokeWidth', 2)
      // .lower();

      this.g
        .append('text')
        .data([radarColumns[i]])
        .attr('class', 'radar-spoke-labels')
        .style('fill', spanChartColors.highlightColor)
        .attr(
          'transform',
          'translate(' + this.centerX + ', ' + this.centerY + ')'
        )
        // .attr('x', Math.cos(theta) * (this.chartR + 5))
        .attr('y', Math.sin(this.thetaOffset + theta) * (this.chartR + 25) - 20)
        .attr('text-anchor', () => {
          if (this.thetaOffset + theta < PI / 2) {
            return 'start'
          } else if (
            this.thetaOffset + theta >= PI / 2 &&
            this.thetaOffset + theta < (3 * PI) / 2
          ) {
            return 'end'
          } else {
            return 'start'
          }
        })
        .attr('dominant-baseline', () => {
          if (this.thetaOffset + theta < PI) {
            return 'baseline'
          } else {
            return 'baseline'
          }
        })
        .attr('font-size', '12px')
        .html(
          this.getLabelText(
            radarColumns[i],
            this.thetaOffset + theta,
            this.chartR
          )
        )
        .on('mouseover', this.indexTip.show)
        .on('mouseout', this.indexTip.hide)
    }
  }

  renderData(metric, country, radarCode) {
    this.svg.selectAll(`.radar-data-${radarCode}`).remove()

    // start getting new data
    var radarColumns
    var metricName
    if (metric === 'mean_overall') {
      metricName = 'Berggruen Index'
      radarColumns = ['mean_inputs', 'mean_throughputs', 'mean_outputs']
    } else if (metric === 'mean_inputs') {
      metricName = 'Democracy'
      radarColumns = [
        'mean_civ.soc',
        'mean_culture',
        'mean_feedback',
        'mean_democratic.engagement',
      ]
    } else if (metric === 'mean_throughputs') {
      metricName = 'Government'
      radarColumns = [
        'mean_analytical',
        'mean_coordination',
        'mean_corruption',
        'mean_impartiality',
        'mean_judicial.impartiality',
        'mean_politicized.bureaucracy',
        'mean_procedural.efficiency',
        'mean_recruitment',
        'mean_regulatory.burden',
        'mean_tax.collection',
        'mean_government.transparency',
      ]
    } else if (metric === 'mean_outputs') {
      metricName = 'Life'
      radarColumns = [
        'mean_civil.justice',
        'mean_crime',
        'mean_business.regulation',
        'mean_other.public.services',
        'mean_education',
        'mean_environment',
        'mean_inflation',
        'mean_price.levels',
        'mean_public.health',
        'mean_shadow.economy',
      ]
    }

    let radarData = []
    let selectedData = this.data.find((el) => el.key === country)['values'][0]

    radarColumns.forEach((el) => {
      const dataEntry = {
        metric: el,
        value: selectedData[el],
      }

      radarData.push(dataEntry)
    })

    let polygonData = radarData.map((el, i) => {
      const arcSize = TWO_PI / radarColumns.length
      const theta = i * arcSize

      return [
        Math.cos(this.thetaOffset + theta) * this.linearScale(el.value),
        Math.sin(this.thetaOffset + theta) * this.linearScale(el.value),
      ]
    })

    for (let i = 0; i < radarColumns.length; i++) {
      const el = radarColumns[i]
      const arcSize = TWO_PI / radarColumns.length
      const theta = i * arcSize

      // draw points along spokes

      if (radarCode == 'a') {
        this.g
          .append('circle')
          .data([radarData[i].value])
          .attr('class', `radar-points radar-data-${radarCode}`)
          .attr(
            'transform',
            'translate(' + this.centerX + ', ' + this.centerY + ')'
          )
          .attr('fill', 'black')
          .attr(
            'cx',
            Math.cos(this.thetaOffset + theta) *
              this.linearScale(radarData[i].value)
          )
          .attr(
            'cy',
            Math.sin(this.thetaOffset + theta) *
              this.linearScale(radarData[i].value)
          )
          .attr('r', 3)
          .raise()
          .on('mouseover', this.tip.show)
          .on('mouseout', this.tip.hide)
      } else {
        var xGroup = this.g
          .append('g')
          .data([radarData[i].value])
          .attr(
            'transform',
            'translate(' +
              Math.cos(this.thetaOffset + theta) *
                this.linearScale(radarData[i].value) +
              ', ' +
              Math.sin(this.thetaOffset + theta) *
                this.linearScale(radarData[i].value) +
              ')'
          )
          .on('mouseover', this.tip.show)
          .on('mouseout', this.tip.hide)

        xGroup
          .append('line')
          .attr('class', `radar-points radar-data-${radarCode}`)
          .attr(
            'transform',
            'translate(' + this.centerX + ', ' + this.centerY + ')'
          )
          .attr('fill', 'black')
          .attr('stroke', 'black')
          .attr('stroke-width', 2)
          .attr('x1', -4)
          .attr('y1', -4)
          .attr('x2', 4)
          .attr('y2', 4)
          .raise()

        xGroup
          .append('line')
          .attr('class', `radar-points radar-data-${radarCode}`)
          .attr(
            'transform',
            'translate(' + this.centerX + ', ' + this.centerY + ')'
          )
          .attr('fill', 'black')
          .attr('stroke', 'black')
          .attr('stroke-width', 2)
          .attr('x1', 4)
          .attr('y1', -4)
          .attr('x2', -4)
          .attr('y2', 4)
          .raise()
      }
    }

    this.g
      .append('polygon')
      .attr('class', `radar-polygon radar-data-${radarCode}`)
      .attr(
        'transform',
        'translate(' + this.centerX + ', ' + this.centerY + ')'
      )
      .attr('points', polygonData)
      .attr('fill', () => {
        if (radarCode === 'a') {
          return radarChartColors[metric].radarColorA
        } else {
          return radarChartColors[metric].radarColorB
        }
      })

    this.g.selectAll('.radar-points').raise()
  }

  getLabelText(id, theta, r) {
    const nameString = getIndexNameFromId(id)
    let nameStringArray = nameString.split(' ')

    if (nameStringArray[0] === 'Quality' && nameStringArray[1] == 'of') {
      nameStringArray.splice(0, 1)
      nameStringArray[0] = 'Quality Of'
    }
    const tspanArray = nameStringArray.map((el, i) => {
      return (
        `<tspan x=${Math.cos(theta) * (r + 20)} dy='1.2em'>` + el + `</tspan>`
      )
    })
    const retval = tspanArray.join('')

    return retval
  }
}
