import React, { Component } from 'react';
import '../styles/UnderOverChart.scss';
import * as d3 from 'd3';
import '../charts/d3OverUnderChart';

import dataFile from '../data/full_dataset.csv';
import { nestData } from '../utils/dataUtils';
import { d3OverUnderChart } from '../charts/d3OverUnderChart';

export class UnderOverChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      width: null,
      height: null,
    };

    this.overUnderChart = null;
  }

  handleDotOver = country => {
    this.props.handleDotHover(country);
  };

  handleDotReset = () => {
    this.props.handleDotReset();
  };

  resizeHandler = () => {
    const cont = d3.select('.under-over-chart-container');

    if (!cont.empty()) {
      this.setState({
        width: cont.style('width'),
        height: cont.style('height'),
      });
    }
  };

  shouldComponentUpdate = (nextProps, nextState) => {
    const { metricX, metricY, hoverCountry } = nextProps;

    if (this.overUnderChart) {
      this.overUnderChart.render({
        metricX: metricX,
        metricY: metricY,
        hoverCountry: hoverCountry,
        width: nextState.width,
        height: nextState.height,
      });
    }

    return false;
  };

  render() {
    return (
      <div className="under-over-chart-container">
        {/* <h2>Comparing Achievement</h2>
        <p className="subtext-block">When comparing two Index Scores, we can examine whether a country’s score in a particular area is higher or lower than expected.</p> */}
        <svg id={this.props.svgName} className="svg-over-under-chart"></svg>
      </div>
    );
  }

  componentDidMount = () => {
    const YEAR = process.env.REACT_APP_YEAR;

    d3.csv(dataFile).then(data => {
      data = nestData(data);
      data = data.find(el => el.key === YEAR).values.map(el => el.values[0]);
      const svg = d3.select(`#${this.props.svgName}`);
      this.overUnderChart = new d3OverUnderChart(
        svg,
        data,
        { top: 0, right: 40, bottom: 20, left: 120 },
        this
      );
      this.overUnderChart.render({
        metricX: this.props.metricX,
        metricY: this.props.metricY,
      });
    });

    window.addEventListener('resize', this.resizeHandler);
  };
}

export default UnderOverChart;
