import React from 'react';
import '../styles/MainIndexSelector.scss';
import { spanChartColors } from '../utils/constants';

function MainIndexSelector(props) {
  const handleClick = () => {
    props.clicked(props.label);
  };

  const nameLookup = {
    Overall: 'mean_overall',
    Democracy: 'mean_inputs',
    Government: 'mean_throughputs',
    Life: 'mean_outputs',
  };

  const isSelectedClass =
    props.label === props.selected
      ? `index-is-selected-${nameLookup[props.label]}`
      : '';

  var isLastItem = props.label === 'Overall' ? 'selector-last' : '';

  return (
    <div
      className={`main-index-selector ${isSelectedClass} ${isLastItem}`}
      onClick={handleClick}
    >
      <div className="main-index-tag-container">
        <div
          className="main-index-color-indicator"
          style={{
            backgroundColor: `${spanChartColors[nameLookup[props.label]]}`,
          }}
        ></div>
        <span className="main-index-name-label noselect">{props.label}</span>
      </div>
    </div>
  );
}

export default MainIndexSelector;
