import React, { Component } from 'react';
import * as d3 from 'd3';
import dataFile from '../data/full_dataset.csv';
import { nestData } from '../utils/dataUtils';
import '../styles/ScatterPlot.scss';
import '../charts/d3ScatterPlot';
import { d3ScatterPlot } from '../charts/d3ScatterPlot';

export class ScatterPlot extends Component {
  constructor(props) {
    super(props);

    this.state = {
      width: null,
      height: null,
    };

    this.scatterPlot = '';
  }

  shouldComponentUpdate(nextProps, nextState) {
    this.scatterPlot.render({
      metricX: nextProps.metricX,
      metricY: nextProps.metricY,
      hoverCountry: nextProps.hoverCountry,
      width: nextState.width,
      height: nextState.height,
    });

    return false;
  }

  handleDotHover = country => {
    this.props.handleDotHover(country);
  };

  handleDotReset = () => {
    this.props.handleDotReset();
  };

  resizeHandler = () => {
    const cont = d3.select('.scatter-plot-container');
    if (!cont.empty()) {
      this.setState({
        width: cont.style('width'),
        height: cont.style('height'),
      });
    }
  };

  render() {
    return (
      <div className="scatter-plot-container">
        <svg id={this.props.svgName} className="scatterplot"></svg>
      </div>
    );
  }

  componentDidMount() {
    const YEAR = process.env.REACT_APP_YEAR;

    d3.csv(dataFile).then(data => {
      data = nestData(data)
        .find(el => el.key === YEAR)
        .values.map(el => el.values[0]);
      const svg = d3.select(`#${this.props.svgName}`);
      this.scatterPlot = new d3ScatterPlot(
        svg,
        data,
        { top: 10, right: 60, bottom: 80, left: 70 },
        this
      );
      this.scatterPlot.render({
        metricX: this.props.metricX,
        metricY: this.props.metricY,
      });
    });

    window.addEventListener('resize', this.resizeHandler);
  }
}

export default ScatterPlot;
