import React, { Component } from 'react';
import '../styles/CompareChartContainer.scss';
import ScatterPlot from '../components/ScatterPlot';
import CompareSelector from '../components/CompareSelector';
import UnderOverChart from '../components/UnderOverChart';
import CompareWelcome from '../components/CompareWelcome';
import { ReactComponent as SwapIcon } from '../images/reload.svg';
import { ReactComponent as InfoIcon } from '../images/info.svg';

import {
  getIndexNameFromId,
  getIndexIDsFromName,
  validateIndexValue,
} from '../utils/textUtils';

export class CompareChartContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      hoverCountry: null,
      displayWelcome: true,
    };
  }

  handleGetStartedButtonPress = () => {
    const newDisplay = !this.state.displayWelcome;
    this.setState({ displayWelcome: newDisplay });
  };

  handleInfoButtonPress = () => {
    const newDisplay = !this.state.displayWelcome;
    this.setState({ displayWelcome: newDisplay });
  };

  handleSelectorChange = (selectorName, newSelectorValue) => {
    const urlParams = this.props.match.params;
    let newURLString;

    if (selectorName === 'compare-selector-x') {
      const yAddon = urlParams.y ? `/${urlParams.y}` : '';
      newURLString = '/compare/' + newSelectorValue + yAddon;
    } else if (selectorName === 'compare-selector-y') {
      const xAddon = urlParams.x ? `/${urlParams.x}` : '/mean_inputs';
      newURLString = '/compare' + xAddon + '/' + newSelectorValue;
    }

    this.props.history.push(newURLString);
  };

  handleDotHover = country => {
    const newDisplay = false;
    this.setState({ hoverCountry: country, displayWelcome: newDisplay });
  };

  handleDotReset = () => {
    this.setState({ hoverCountry: null });
  };

  handleSwapButtonClick = () => {
    const urlParams = this.props.match.params;
    let newURLString = undefined;

    if (urlParams.x && urlParams.y) {
      // simply swap both
      newURLString = `/compare/${urlParams.y}/${urlParams.x}`;
    } else if (urlParams.x && !urlParams.y) {
      // swap whatever's in x with mean_throughputs (default)
      newURLString = `/compare/mean_throughputs/${urlParams.x}`;
    } else if (!urlParams.x) {
      // swap the default options
      newURLString = `/compare/mean_throughputs/mean_inputs`;
    }

    if (newURLString) {
      this.props.history.push(newURLString);
    }
  };

  render() {
    let renderIndices = {};
    renderIndices['x'] =
      validateIndexValue(this.props.match.params.x) || 'mean_inputs';
    renderIndices['y'] =
      validateIndexValue(this.props.match.params.y) || 'mean_throughputs';

    var chartOrWelcome, infoButton;
    if (this.state.displayWelcome) {
      infoButton = <div></div>;

      chartOrWelcome = (
        <CompareWelcome clicked={this.handleGetStartedButtonPress} />
      );
    } else {
      infoButton = (
        <div className="info-button-container">
          <div className="info-button" onClick={this.handleInfoButtonPress}>
            <InfoIcon className="info-button-icon" />
          </div>
        </div>
      );

      chartOrWelcome = (
        <UnderOverChart
          svgName="svg-over-under-plot"
          metricX={renderIndices.x}
          metricY={renderIndices.y}
          hoverCountry={this.state.hoverCountry}
          handleDotHover={this.handleDotHover}
          handleDotReset={this.handleDotReset}
        />
      );
    }

    return (
      <div className="compare-chart-container">
        <div className="compare-selectors-and-title-container">
          <div className="compare-subtitle-container">
            <span className="compare-chart-title">Compare Data</span>
            <p className="intro-text">
              Hover over a point on the graph to inspect its achievement
              compared to other countries.
            </p>
          </div>
          <div className="compare-selectors-container">
            <CompareSelector
              label="X Value"
              id="compare-selector-x"
              value={renderIndices.x}
              handleChange={this.handleSelectorChange}
            />
            <div
              className="swap-button noselect"
              onClick={this.handleSwapButtonClick}
            >
              <SwapIcon />
            </div>
            <CompareSelector
              label="Y Value"
              id="compare-selector-y"
              value={renderIndices.y}
              handleChange={this.handleSelectorChange}
            />
          </div>
        </div>
        <div className="compare-plots-container">
          <ScatterPlot
            svgName="svg-scatter-plot"
            metricX={renderIndices.x}
            metricY={renderIndices.y}
            hoverCountry={this.state.hoverCountry}
            handleDotHover={this.handleDotHover}
            handleDotReset={this.handleDotReset}
          />
          <div className="right-side-container UI-panel">
            <div className="top-text-panel">
              <div className="text-area">
                <h2>Comparing Achievement</h2>
              </div>
              {infoButton}
            </div>
            {chartOrWelcome}
          </div>
        </div>
      </div>
    );
  }

  componentDidMount() {}
}

export default CompareChartContainer;
