import { scatterPlotColors } from '../utils/constants';
import { calcLinearFit } from '../utils/dataUtils';
import {
  layoutTextLabel,
  layoutGreedy,
  layoutLabel,
  layoutRemoveOverlaps,
} from 'd3fc-label-layout';
import { getIndexNameFromId, getURLNameFromRealName } from '../utils/textUtils';
const d3 = require('d3');

export class d3ScatterPlot {
  constructor(svg, data, margin, parent) {
    this.data = data;
    this.svg = svg;
    this.margin = margin;
    this.parent = parent;

    this.container = d3.select('.scatter-plot-container');
    const containerHeight = this.container.style('height');
    this.svg.style('height', containerHeight);

    this.width =
      Math.round(+svg.style('width').replace('px', '')) -
      this.margin.left -
      this.margin.right;
    this.height =
      Math.round(+svg.style('height').replace('px', '')) -
      this.margin.top -
      this.margin.bottom;

    this.xScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([0, this.width]);
    this.yScale = d3
      .scaleLinear()
      .domain([100, 0])
      .range([0, this.height]);
    this.colorScaleAbove = d3
      .scaleLinear()
      .domain([0, 1])
      .range([scatterPlotColors.middleColor, scatterPlotColors.highColor]);
    this.colorScaleBelow = d3
      .scaleLinear()
      .domain([0, 1])
      .range([scatterPlotColors.middleColor, scatterPlotColors.lowColor]);

    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale);

    this.g = this.svg
      .append('g')
      .attr('transform', 'translate(' + margin.left + ', ' + margin.top + ')');

    this.defs = this.svg.append('defs');
  }

  resize(pixWidth, pixHeight) {
    // const containerHeight = this.container.style('height');
    // this.svg.style('height', containerHeight)

    this.width =
      Math.round(+pixWidth.replace('px', '')) -
      this.margin.left -
      this.margin.right;
    this.height =
      Math.round(+pixHeight.replace('px', '')) -
      this.margin.top -
      this.margin.bottom;
    this.xScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([0, this.width]);
    this.yScale = d3
      .scaleLinear()
      .domain([100, 0])
      .range([0, this.height]);
    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale);
  }

  drawAxis(metricX, metricY) {
    /// X Axis
    d3.selectAll('.scatter-axis-label').remove();

    this.xAxis = d3.axisBottom(this.xScale);

    var x = this.g.selectAll('.scatter-xaxis').data(['dummy']);
    x.enter()
      .append('g')
      .merge(x)
      .call(this.xAxis)
      .attr('class', 'scatter-xaxis')
      .attr('transform', 'translate(' + 0 + ', ' + this.height + ')');

    // label
    this.g
      .append('text')
      .attr('class', 'scatter-xaxis-label scatter-axis-label')
      .attr(
        'transform',
        'translate(' +
          this.width / 2 +
          ' ,' +
          (this.height + this.margin.top + 30) +
          ')'
      )
      .style('text-anchor', 'middle')
      .text(getIndexNameFromId(metricX));

    // grid lines
    this.g.selectAll('.scatter-horizontal-grid-line').remove();
    this.g
      .selectAll('.scatter-horizontal-grid-line')
      .data([20, 40, 60, 80, 100])
      .enter()
      .append('line')
      .attr('class', 'scatter-horizontal-grid-line scatter-grid-line')
      .attr('x1', this.width)
      .attr('y1', d => this.yScale(d))
      .attr('y2', 0)
      .attr('y2', d => this.yScale(d))
      .attr('stroke', scatterPlotColors.gridLineColor)
      .attr('stroke-width', 1);

    /// Y Axis
    this.yAxis = d3.axisLeft(this.yScale);

    var y = this.g.selectAll('.scatter-yaxis').data(['dummy']);
    y.enter()
      .append('g')
      .merge(y)
      .call(this.yAxis)
      .attr('class', 'scatter-yaxis');

    // label
    this.g
      .append('text')
      .attr('class', 'scatter-yaxis-label scatter-axis-label')
      .attr('transform', 'rotate(-90)')
      .attr('y', 0 - this.margin.left + 10)
      .attr('x', 0 - this.height / 2)
      .attr('dy', '1em')
      .style('text-anchor', 'middle')
      .text(getIndexNameFromId(metricY));

    // grid lines
    this.g.selectAll('.scatter-vertical-grid-line').remove();
    this.g
      .selectAll('.scatter-vertical-grid-line')
      .data([20, 40, 60, 80, 100])
      .enter()
      .append('line')
      .attr('class', 'scatter-vertical-grid-line scatter-grid-line')
      .attr('x1', d => this.xScale(d))
      .attr('y1', this.height)
      .attr('x2', d => this.xScale(d))
      .attr('y2', 0)
      .attr('stroke', scatterPlotColors.gridLineColor)
      .attr('stroke-width', 1);
  }

  calcUnderOver(fitSlope, fitIntercept, x, y) {
    const pointOnLine = fitSlope * x + fitIntercept;
    const diff = y - pointOnLine;

    return diff;
  }

  render(props) {
    const { metricX, metricY, hoverCountry, width, height } = props;

    // console.log(d3.select('#svg-scatter-plot').style('height'))
    // console.log(this.height)

    if (width && height) {
      this.resize(width, height);
    }

    const xSeries = this.data.map(el => +el[metricX]);
    const ySeries = this.data.map(el => +el[metricY]);
    const [fitSlope, fitInterept, fitRSquare] = calcLinearFit(xSeries, ySeries);

    // create subdataset that changes if metric x or y changes
    const scatterData = this.data.map(el => {
      const retval = {
        country: el.country,
        x: +el[metricX],
        y: +el[metricY],
        underOver: this.calcUnderOver(
          fitSlope,
          fitInterept,
          +el[metricX],
          +el[metricY]
        ),
      };

      return retval;
    });

    // set color scale based on range of under/over values
    const underOverSeries = scatterData.map(el => el.underOver);
    const underOverExtent = d3.extent(underOverSeries);
    const [underOverMin, underOverMax] = underOverExtent;
    this.colorScaleAbove.domain([0, underOverMax]);
    this.colorScaleBelow.domain([0, underOverMin]);

    // this.drawGradientBlocks(fitSlope, fitInterept);
    this.drawAxis(metricX, metricY);

    // create event functions for double-scoping
    const handleHoverOver = dotThis => {
      const element = d3.select(dotThis);
      const data = element.data()[0];

      this.parent.handleDotHover(data.country);
    };

    const handleHoverReset = dotThis => {
      this.parent.handleDotReset();
    };

    var scatterGroups = this.g.selectAll('.scatter-group').data(scatterData);

    scatterGroups.exit().remove();

    // create groups for new entries
    var enter = scatterGroups
      .enter()
      .append('g')
      .attr(
        'class',
        d => `scatter-group scatter-group-${getURLNameFromRealName(d.country)}`
      )
      .on('mouseover', function() {
        handleHoverOver(this);
      })
      .on('mouseout', function() {
        handleHoverReset(this);
      });

    enter
      .append('circle')
      .attr('class', d => `scatter-circle `)
      .attr('cx', 0)
      .attr('cy', 0)
      .attr('r', 5)
      .attr('stroke-width', 1)
      .attr('stroke', 'rgba(0,0,0,0)');

    enter
      .append('circle')
      .attr('class', 'scatter-circle-hover-target')
      .attr('cx', 0)
      .attr('cy', 0)
      .attr('r', 14)
      .attr('fill', 'rgba(0, 0, 0, 0)')
      .attr('stroke', 'none')
      .raise();

    const scatterGroupMerge = scatterGroups.merge(enter);
    scatterGroupMerge
      .raise()
      .transition()
      .duration(0)
      .attr('transform', d => {
        return 'translate(' + this.xScale(d.x) + ', ' + this.yScale(d.y) + ')';
      });

    scatterGroupMerge
      .select('.scatter-circle')
      .transition()
      .duration(0)
      .style('fill', d => {
        if (d.underOver === 0) {
          return scatterPlotColors.fitLineColor;
        } else if (d.underOver > 0) {
          return this.colorScaleAbove(d.underOver);
        } else if (d.underOver < 0) {
          return this.colorScaleBelow(d.underOver);
        }
      });

    // dynamic labelPlacement via plugin
    const labelPadding = 3;
    const textLabel = layoutTextLabel()
      .padding(labelPadding)
      .value(d => d.country);

    // const strategy = layoutRemoveOverlaps(layoutGreedy());
    const strategy = layoutGreedy();

    const labels = layoutLabel(strategy)
      .size((d, i, g) => {
        const textSize = g[i].getElementsByTagName('text')[0].getBBox();
        return [
          textSize.width + labelPadding * 2,
          textSize.height + labelPadding * 2,
        ];
      })
      .position(d => {
        return [this.xScale(d.x), this.yScale(d.y)];
      })
      .component(textLabel);

    this.g.datum(scatterData).call(labels);

    // draw fit line
    // only draw fit line if there are two metrics showing
    if (metricX && metricY) {
      const y1 = fitInterept;
      const y2 = fitSlope * 100 + fitInterept;

      this.g.selectAll('.scatter-fit-line').remove();
      this.g
        .append('line')
        .attr('class', 'scatter-fit-line')
        .attr('x1', this.xScale(0))
        .attr('y1', this.yScale(y1))
        .attr('x2', this.xScale(100))
        .attr('y2', this.yScale(y2))
        .attr('stroke', scatterPlotColors.fitLineColor)
        .attr('fill', 'none');
    }

    // bind mouseover functions to labels
    this.svg
      .selectAll('.label')
      .on('mouseover', function() {
        handleHoverOver(this);
      })
      .on('mouseout', function() {
        handleHoverReset();
      })
      .raise();

    //if any lines are hovered, draw hover lines
    this.g
      .selectAll('.scatter-highlight-line, .scatter-highlight-node')
      .remove();
    if (hoverCountry) {
      //get data for that country
      const hoverData = scatterData.find(el => el.country === hoverCountry);
      const pointOnLine = fitSlope * hoverData.x + fitInterept;

      // create gradient
      this.svg.select('#linear-gradient').remove();
      var linearGradient = this.defs.append('linearGradient');

      if (hoverData.underOver === 0) {
        linearGradient
          .attr('id', 'linear-gradient')
          .attr('x1', '0%')
          .attr('y1', '0%')
          .attr('x2', '0%')
          .attr('y2', '100%');

        linearGradient
          .append('stop')
          .attr('offset', '0%')
          .attr('stop-color', this.colorScaleAbove(hoverData.underOver));
      } else if (hoverData.underOver > 0) {
        linearGradient
          .attr('id', 'linear-gradient')
          .attr('x1', '0%')
          .attr('y1', '0%')
          .attr('x2', '0%')
          .attr('y2', '100%');

        linearGradient
          .append('stop')
          .attr('offset', '0%')
          .attr('stop-color', this.colorScaleAbove(hoverData.underOver));

        linearGradient
          .append('stop')
          .attr('offset', '100%')
          .attr('stop-color', this.colorScaleAbove(0));
      } else if (hoverData.underOver < 0) {
        linearGradient
          .attr('id', 'linear-gradient')
          .attr('x1', '0%')
          .attr('y1', '100%')
          .attr('x2', '0%')
          .attr('y2', '0%');

        linearGradient
          .append('stop')
          .attr('offset', '0%')
          .attr('stop-color', this.colorScaleBelow(hoverData.underOver));

        linearGradient
          .append('stop')
          .attr('offset', '100%')
          .attr('stop-color', this.colorScaleBelow(0));
      }

      if (this.yScale(hoverData.y) - this.yScale(pointOnLine) < 0) {
        // draw rect going down
        this.g
          .append('rect')
          .attr('class', 'scatter-highlight-line')
          .attr('x', this.xScale(hoverData.x) - 5)
          .attr('y', this.yScale(hoverData.y))
          .attr('width', 10)
          .attr(
            'height',
            Math.abs(this.yScale(hoverData.y) - this.yScale(pointOnLine))
          )
          .attr('fill', 'url(#linear-gradient)')
          .attr('stroke', 'black')
          .attr('stroke-width', 0);
      } else {
        // draw rect going up
        this.g
          .append('rect')
          .attr('class', 'scatter-highlight-line')
          .attr('x', this.xScale(hoverData.x) - 5)
          .attr('y', this.yScale(pointOnLine))
          .attr('width', 10)
          .attr(
            'height',
            Math.abs(this.yScale(hoverData.y) - this.yScale(pointOnLine))
          )
          .attr('fill', 'url(#linear-gradient)')
          .attr('stroke', 'black')
          .attr('stroke-width', 0);
      }

      this.g
        .append('circle')
        .attr('class', 'scatter-highlight-node')
        .attr('cx', this.xScale(hoverData.x))
        .attr('cy', this.yScale(pointOnLine))
        .attr('r', 5)
        // .attr('stroke', 'black')
        .attr('fill', scatterPlotColors.fitLineColor);

      this.svg
        .select(`.scatter-group-${getURLNameFromRealName(hoverData.country)}`)
        .append('circle')
        .attr('class', 'scatter-highlight-node')
        .attr('cx', 0)
        .attr('cy', 0)
        .attr('r', 3)
        .style('fill', 'white');

      // d3.selectAll('.scatter-group').raise();
      this.svg.selectAll(' .scatter-highlight-line').lower();
      this.svg.selectAll('.scatter-grid-line').lower();
    }
    this.svg.selectAll('.scatter-circle-hover-target').raise();
  }
}
