import * as d3 from 'd3';
import { calcLinearFit } from '../utils/dataUtils';
import { overUnderColors } from '../utils/constants';



export class d3OverUnderChart {

  constructor(svg, data, margin, parent) {

    this.data = data;
    this.svg = svg;
    this.margin = margin;
    this.parent = parent;

    this.container = d3.select('.under-over-chart-container');
    const containerHeight = this.container.style('height');
    this.svg.style('height', containerHeight);

    this.width = Math.round(+svg.style('width').replace('px', '')) - this.margin.left - this.margin.right;
    this.height = Math.round(+svg.style('height').replace('px', '')) - this.margin.top - this.margin.bottom;
    this.colorScaleAbove = d3.scaleLinear().domain([0, 1]).range([overUnderColors.middleColor, overUnderColors.highColor]);
    this.colorScaleBelow = d3.scaleLinear().domain([0, 1]).range([overUnderColors.middleColor, overUnderColors.lowColor]);

    this.xScale = d3.scaleLinear();
    this.yScale = d3.scaleBand();


    this.xAxis = null;
    this.yAxis = null;

    this.defs = this.svg.append('defs');

    this.g = this.svg.append('g')
      .attr("transform", "translate(" + margin.left + ", " + margin.top + ")");

  }

  calculateOverUnder(props) {

    const { metricX, metricY } = props;

    const dataXY = this.data.map(el => {
      return {
        country: el.country,
        x: +el[metricX],
        y: +el[metricY]
      }
    })

    const xSeries = dataXY.map(el => el.x);
    const ySeries = dataXY.map(el => el.y);

    const [fitSlope, fitIntercept, fitRSquare] = calcLinearFit(xSeries, ySeries);

    const calcPosition = dataXY.map(el => {

      const fitValue = el.x * fitSlope + fitIntercept;
      const fitDiff = el.y - fitValue;

      return {
        country: el.country,
        fitValue: fitValue,
        fitDiff: fitDiff,
      }

    })

    const calcPositionSorted = calcPosition.sort((a, b) => b.fitDiff - a.fitDiff);

    return calcPositionSorted;

  }

  resize(pixWidth, pixHeight) {
    this.width = Math.round(+pixWidth.replace('px', '')) - this.margin.left - this.margin.right;
    this.height = Math.round(+pixHeight.replace('px', '')) - this.margin.top - this.margin.bottom;
  }


  createScales(data) {

    const diffs = (data.map(el => el.fitDiff));
    //create x scale from min and max of the fitDiff value
    this.xScale
      .domain([d3.min(diffs) - 5, d3.max(diffs) + 5])
      .range([0, this.width]);

    //create y scale from band of all the countries
    this.yScale
      .domain(data.map(el => el.country))
      .range([this.margin.top, this.height]);


  }

  createAndDrawAxis() {

    d3.selectAll('.over-under-xaxis, .over-under-yaxis').remove();

    this.xAxis = d3.axisBottom(this.xScale).ticks(3);
    this.yAxis = d3.axisLeft(this.yScale);


    var x = this.g.selectAll('.over-under-xaxis').data(['dummy']);
    x.enter().append('g').merge(x).call(this.xAxis)
      .attr('class', 'over-under-xaxis')
      .attr("transform", "translate(" + 0 + ", " + this.height + ")");


    var y = this.g.selectAll('.over-under-yaxis').data(['dummy']);
    y.enter().append('g').merge(y).call(this.yAxis)
      .attr('class', 'over-under-yaxis')

    this.g.selectAll('.over-under-vertical-grid-line').remove();
    this.g.selectAll('.over-under-vertical-grid-line').data([0])
      .enter().append('line')
      .attr('class', 'over-under-vertical-grid-line over-under-grid-line')
      .attr('x1', d => this.xScale(d))
      .attr('y1', this.height)
      .attr('x2', d => this.xScale(d))
      .attr('y2', 0)
      .attr('stroke', overUnderColors.centerLineColor)
    // .attr('stroke-dasharray', "2,2")

  }



  render(props) {

    const { metricX, metricY, hoverCountry, width, height } = props;

    const chartData = this.calculateOverUnder({
      metricX: metricX,
      metricY: metricY
    });

    if (width && height) {
      this.resize(width, height);
    }

    this.createScales(chartData);
    this.createAndDrawAxis();

    //calculate color scale
    const fitDiffSeries = chartData.map(el => el.fitDiff);
    const underOverExtent = d3.extent(fitDiffSeries);
    const [underOverMin, underOverMax] = underOverExtent;
    this.colorScaleAbove.domain([0, underOverMax]);
    this.colorScaleBelow.domain([0, underOverMin]);


    // create gradient 
    this.svg.select('#linear-gradient2').remove();
    let linearGradient = this.defs.append('linearGradient')


    // create functions for double scoping
    const handleMouseOver = (dotThis) => {
      const element = d3.select(dotThis);
      const data = element.data()[0];

      this.parent.handleDotOver(data.country)
    }

    const handleMouseOut = (dotThis) => {
      this.parent.handleDotReset();
    }



    // draw horizontal lines
    var spanLines = this.g.selectAll('.over-under-span-line').data(chartData);
    spanLines.enter().append('line').merge(spanLines)
      .attr('class', 'over-under-span-line')
      .attr('x1', 0)
      .attr('y1', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('x2', this.width)
      .attr('y2', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('fill', 'none')
      .attr('stroke', '#DCDCDC');





    // draw lines connected to circles
    var markerLines = this.g.selectAll('.over-under-marker-line').data(chartData);
    markerLines.enter().append('rect').merge(markerLines)
      .attr('class', 'over-under-marker-line')
      .attr('x', d => Math.min(this.xScale(0), this.xScale(d.fitDiff)))
      .attr('y', d => {
        if (d.country === hoverCountry) {
          return this.yScale(d.country) + this.yScale.bandwidth() / 2 - 5
        } else {
          return this.yScale(d.country) + this.yScale.bandwidth() / 2 - 0.5
        }
      })
      .attr('height', d => d.country === hoverCountry ? 10 : 1)
      .attr('width', d => Math.abs(this.xScale(0) - this.xScale(d.fitDiff)))
      .attr('fill', d => {
        if (d.country === hoverCountry) {
          linearGradient.append('stop')
            .attr('offset', '0%')
            .attr('stop-color', overUnderColors.centerLineColor)

          if (d.fitDiff === 0) {
            return 'white'
          } else if (d.fitDiff > 0) {

            linearGradient
              .attr('id', 'linear-gradient2')
              .attr('x1', '0%')
              .attr('y1', '0%')
              .attr('x2', '100%')
              .attr('y2', '0%')

            linearGradient.append('stop')
              .attr('offset', '100%')
              .attr('stop-color', this.colorScaleAbove(d.fitDiff))
            return 'url(#linear-gradient2)'
          } else if (d.fitDiff < 0) {
            linearGradient
              .attr('id', 'linear-gradient2')
              .attr('x1', '100%')
              .attr('y1', '0%')
              .attr('x2', '0%')
              .attr('y2', '0%')

            linearGradient.append('stop')
              .attr('offset', '100%')
              .attr('stop-color', this.colorScaleBelow(d.fitDiff))
            return 'url(#linear-gradient2)'
          }
        }
        else {
          return '#939393'
        }
      })


    // draw circles
    var markers = this.g.selectAll('.over-under-marker-group').data(chartData);

    markers.exit().remove();

    let markersEnter = markers.enter().append('g')
      .attr('class', 'over-under-marker-group')
      .on('mouseover', function () { handleMouseOver(this) })
      .on('mouseout', function () { handleMouseOut(this) });

    markersEnter.append('circle')
      .attr('class', `over-under-marker`)

    markersEnter.append('circle')
      .attr('class', 'over-under-highlight-circle')


    const markersMerge = markers.merge(markersEnter);

    markersMerge.select('.over-under-marker')
      .attr('cx', d => this.xScale(d.fitDiff))
      .attr('cy', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('r', d => {
        if (d.country === hoverCountry) {
          return 5;
        } else {
          return 5;
        }
      })
      .attr('fill', d => {
        if (d.fitDiff === 0) {
          return 'white'
        } else if (d.fitDiff > 0) {
          return this.colorScaleAbove(d.fitDiff);
        } else if (d.fitDiff < 0) {
          return this.colorScaleBelow(d.fitDiff);
        }
      })

    markersMerge.select('.over-under-highlight-circle')
      .attr('cx', d => this.xScale(d.fitDiff))
      .attr('cy', d => this.yScale(d.country) + this.yScale.bandwidth() / 2)
      .attr('r', d => {
        if (d.country === hoverCountry) {
          return 3;
        } else {
          return 0;
        }
      })
      .attr('fill', 'white')
      .attr('stroke', 'none');

    if (hoverCountry) {
      // get position in list
      const posInList = chartData.map(el => el.country).findIndex(el => el === hoverCountry)

      d3.selectAll('.over-under-yaxis>g>text').each(function (d, i) {
        if (i === posInList) {
          d3.select(this)
            .style('font-weight', 'bold')
            .style('color', overUnderColors.textHighlightColor);
        }
      })
    }

  }

}
