import React from 'react';
import Select from 'react-select';
import { explorePageColors, dropdownStyles } from '../utils/constants';

function CountrySelector(props) {
  const handleChange = e => {
    props.handleChange(null, e.value);
  };

  const fieldList = [
    'Argentina',
    'Australia',
    'Brazil',
    'Canada',
    'Chile',
    'China',
    'Colombia',
    'Egypt',
    'France',
    'Ghana',
    'Germany',
    'Georgia',
    'India',
    'Indonesia',
    'Israel',
    'Italy',
    'Japan',
    'Jordan',
    'Kazakhstan',
    'Kenya',
    'Mexico',
    'Netherlands',
    'Nigeria',
    'Norway',
    'Pakistan',
    'Philippines',
    'Russia',
    'Saudi Arabia',
    'South Africa',
    'South Korea',
    'Spain',
    'Sweden',
    'Switzerland',
    'Turkey',
    'Ukraine',
    'United Kingdom',
    'United States',
    'Vietnam',
  ];

  const selectOptions = fieldList.map(el => {
    return {
      value: el,
      label: el,
    };
  });

  const customTheme = theme => {
    return {
      ...theme,
      colors: {
        ...theme.colors,
        primary: explorePageColors.dropdownPrimaryColor,
        primary25: explorePageColors.dropdownPrimaryColor25,
        primary50: explorePageColors.dropdownPrimaryColor50,
        primary75: explorePageColors.dropdownPrimaryColor75,
      },
    };
  };

  return (
    <div className="country-selector-container">
      <div className="field-label">{'Choose a Different Country'}</div>
      <Select
        className="country-selector"
        options={selectOptions}
        value={{ value: props.value, label: props.value }}
        onChange={handleChange}
        theme={customTheme}
        styles={dropdownStyles}
      />
    </div>
  );
}

export default CountrySelector;
