export const getIndexNameFromId = (id) => {
  try {
    return indexDataLookup.find((el) => el.id === id).name
  } catch (error) {
    return undefined
  }
}

export const getIndexIDsFromName = (name) => {
  try {
    return indexDataLookup.find((el) => el.name === name).id
  } catch (error) {
    return undefined
  }
}

export const getRealNameFromURL = (url_name) => {
  try {
    return countryURLLookup.find((el) => el.url_string === url_name).real_name
  } catch (error) {
    return undefined
  }
}

export const getURLNameFromRealName = (real_name) => {
  return countryURLLookup.find((el) => el.real_name === real_name).url_string
}

export const getIndexTitleAndDescription = (index) => {
  const YEAR = process.env.REACT_APP_YEAR

  if (index) {
    return indexDescriptionLookup[index]
  } else {
    return {
      title: `Berggruen Index ${+YEAR + 1}`,
      description:
        'Select a data point on the chart or choose an index above to get started.',
    }
  }
}

export const assembleIndexTooltip = (index) => {
  const description = indexDataLookup.find((el) => el.id === index).description
  const dataSource = indexDataLookup.find((el) => el.id === index).data_source

  return `<span class="index-tool-tip-description">${description}</span> <span class="index-tooltip-data-source"> ${dataSource}</span>`
}

export const validateIndexValue = (index) => {
  const indexList = indexDataLookup.map((el) => el.id)
  return indexList.includes(index) ? index : undefined
}
