import React, { Component } from 'react';
import SpanChart from '../components/SpanChart';
import RadarSideBar from './RadarSideBar';
import MainIndexSelector from '../components/MainIndexSelector';
import SpanSortControl from '../components/SpanSortControl';
import '../styles/OverviewChartContainer.scss';
import * as d3 from 'd3';

export class OverviewChartContainer extends Component {
  state = {
    metrics: [
      'mean_overall',
      'mean_inputs',
      'mean_throughputs',
      'mean_outputs',
    ],
    metric: null,
    radarCountryA: null,
    radarCountryB: null,
    metricIsSelected: false,
    selectedMainIndex: null,
    sortParameter: 'mean_overall',
  };

  handleSelectedMainIndex = index => {
    let newIndex, newMetric, newMetricIsSelected;

    if (this.state.selectedMainIndex === index) {
      newIndex = null;
      newMetric = null;
      newMetricIsSelected = false;
    } else {
      newIndex = index;
      newMetricIsSelected = true;
      switch (index) {
        case 'Democracy':
          newMetric = 'mean_inputs';
          break;
        case 'Government':
          newMetric = 'mean_throughputs';
          break;
        case 'Life':
          newMetric = 'mean_outputs';
          break;
        case 'Overall':
          newMetric = 'mean_overall';
          break;

        default:
          newIndex = null;
          break;
      }
    }

    if (newMetric) {
      // d3.selectAll(`.marker-${this.props.activeMetric}`).raise();
      d3.selectAll(`.marker-${newMetric}`).raise();
    }

    this.setState({
      selectedMainIndex: newIndex,
      metric: newMetric,
      metricIsSelected: newMetricIsSelected,
      radarCountryA: null,
      radarCountryB: null,
    });
  };

  handleSortChange = sort => {
    let newSort = sort;

    if (newSort !== this.state.sortParameter) {
      this.setState({ sortParameter: newSort });
    }
  };

  handleSpanDotHover = (country, metric) => {
    let newSelectedMainIndex;

    switch (metric) {
      case 'mean_inputs':
        newSelectedMainIndex = 'Democracy';
        break;
      case 'mean_throughputs':
        newSelectedMainIndex = 'Government';
        break;
      case 'mean_outputs':
        newSelectedMainIndex = 'Life';
        break;
      case 'mean_overall':
        newSelectedMainIndex = 'Overall';
        break;
      default:
        newSelectedMainIndex = null;
        break;
    }

    if (!this.state.metricIsSelected) {
      this.setState({
        metric: metric,
        radarCountryA: country,
        selectedMainIndex: newSelectedMainIndex,
      });
    }
  };

  handleSpanHoverReset = () => {
    if (!this.state.metricIsSelected) {
      this.setState({ metric: null, selectedMainIndex: null });
    }
  };

  handleTagClear = country => {
    var newMetricIsSelected, newCountryA, newCountryB, newMetric;
    newCountryA = this.state.radarCountryA;
    newCountryB = this.state.radarCountryB;
    newMetricIsSelected = this.state.metricIsSelected;
    newMetric = this.state.metric;

    if (this.state.radarCountryA == country) {
      // undo country A
      newCountryA = null;

      if (!newCountryA && !newCountryB) {
        newMetricIsSelected = false;
        newMetric = null;
      }
    } else if (this.state.radarCountryB == country) {
      // undo country B
      newCountryB = null;
      if (!newCountryA && !newCountryB) {
        newMetricIsSelected = false;
        newMetric = null;
      }
    }

    this.setState({
      metricIsSelected: newMetricIsSelected,
      radarCountryA: newCountryA,
      radarCountryB: newCountryB,
      metric: newMetric,
    });
  };

  handleSpanClick = (country, metric) => {
    var newMetricIsSelected, newCountryA, newCountryB;
    newCountryA = this.state.radarCountryA;
    newCountryB = this.state.radarCountryB;
    newMetricIsSelected = this.state.metricIsSelected;

    if (!this.state.metricIsSelected) {
      // flip on selected state - country A will be preserved

      newMetricIsSelected = !this.state.metricIsSelected;
      this.setState({ metricIsSelected: newMetricIsSelected });
    } else if (this.state.metricIsSelected) {
      if (this.state.metric == metric) {
        // handle purple dot clicked
        if (this.state.radarCountryA == country) {
          // undo country A
          newCountryA = null;

          if (!newCountryA && !newCountryB) {
            newMetricIsSelected = false;
          }
        } else if (this.state.radarCountryB == country) {
          // undo country B
          newCountryB = null;
          if (!newCountryA && !newCountryB) {
            newMetricIsSelected = false;
          }
        } else {
          if (
            this.state.radarCountryA != null &&
            this.state.radarCountryB != null
          ) {
          } else if (
            this.state.radarCountryA != null &&
            this.state.radarCountryB == null
          ) {
            // add click as country B
            newCountryB = country;
          } else if (
            this.state.radarCountryA == null &&
            this.state.radarCountryB != null
          ) {
            // add click as country A
            newCountryA = country;
          } else if (
            this.state.radarCountryA === null &&
            this.state.radarCountryB === null
          ) {
            newCountryA = country;
          }
        }

        this.setState({
          metricIsSelected: newMetricIsSelected,
          radarCountryA: newCountryA,
          radarCountryB: newCountryB,
        });
      }
    }
  };

  render() {
    return (
      <div className="overview-chart-container">
        <div className="left-side-panel">
          <div className="spanchart-controls-container-left">
            <div className="overview-title-container">
              <div className="overview-title">Report Overview</div>
              <p className="intro-text">
                Click a data point on the chart or choose a Main Index to get
                started.
              </p>
            </div>
            <div className="span-sort-control-container">
              <SpanSortControl
                sortParameter={this.state.sortParameter}
                clicked={this.handleSortChange}
              />
            </div>
          </div>
          <SpanChart
            svgName="svg-spanchart"
            metrics={this.state.metrics}
            handleSpanDotHover={this.handleSpanDotHover}
            handleSpanHoverReset={this.handleSpanHoverReset}
            handleSpanClick={this.handleSpanClick}
            radarCountryA={this.state.radarCountryA}
            radarCountryB={this.state.radarCountryB}
            metricIsSelected={this.state.metricIsSelected}
            activeMetric={this.state.metric}
            sortValue={this.state.sortParameter}
          ></SpanChart>
        </div>

        <div className="right-side-panel">
          <div className="spanchart-controls-container-right">
            <div className="main-index-controls-label-container">
              <span className="field-label">main index</span>
            </div>
            <div className="main-index-controls-container">
              <MainIndexSelector
                label="Democracy"
                selected={this.state.selectedMainIndex}
                clicked={this.handleSelectedMainIndex}
              />
              <MainIndexSelector
                label="Government"
                selected={this.state.selectedMainIndex}
                clicked={this.handleSelectedMainIndex}
              />
              <MainIndexSelector
                label="Life"
                selected={this.state.selectedMainIndex}
                clicked={this.handleSelectedMainIndex}
              />
              <MainIndexSelector
                label="Overall"
                selected={this.state.selectedMainIndex}
                clicked={this.handleSelectedMainIndex}
              />
            </div>
          </div>
          <RadarSideBar
            metric={this.state.metric}
            radarCountryA={this.state.radarCountryA}
            radarCountryB={this.state.radarCountryB}
            metricIsSelected={this.state.metricIsSelected}
            handleTagClear={this.handleTagClear}
          ></RadarSideBar>
        </div>
      </div>
    );
  }
}

export default OverviewChartContainer;
