import React, { Component } from 'react'
import * as d3 from 'd3'
import '../styles/RadarChart.scss'
import { nestData } from '../utils/dataUtils'
import { d3SideRadarChart } from '../charts/d3SideRadarChart'

export class RadarChart extends Component {
  constructor(props) {
    super(props)

    this.state = {
      width: null,
      height: null,
      dataLoaded: false,
    }

    this.svgName = props.svgName
    this.radarChart = ''
  }

  shouldComponentUpdate = (nextProps, nextState) => {
    const { metric, radarCountryA, radarCountryB } = nextProps

    // d3.selectAll('.radar-points, .radar-polygon, .radar-spokes, .radar-spoke-labels, .radar-container').remove();

    if (nextState.height && nextState.width) {
      this.radarChart.resize(nextState.width, nextState.height)
    } else {
      // this.radarChart.resize(`${window.innerWidth * 0.4}px`, `${window.innerHeight}px`)
    }

    if (metric !== null) {
      this.radarChart.renderSpokes(metric)

      if (radarCountryA) {
        this.radarChart.renderData(metric, radarCountryA, 'a')
      }

      if (radarCountryB) {
        this.radarChart.renderData(metric, radarCountryB, 'b')
      }
    }

    return false
  }

  handleResize = () => {
    const cont = d3.select(`#${this.svgName}`)

    if (!cont.empty()) {
      this.setState({
        width: cont.style('width'),
        height: cont.style('height'),
      })
    }
  }

  render() {
    return (
      <div className="radar-chart-container">
        <svg className="radar-chart-svg" id={this.svgName}></svg>
      </div>
    )
  }

  componentDidMount = () => {
    const YEAR = process.env.REACT_APP_YEAR

    const data = nestData(this.props.data).find((el) => el.key === YEAR).values
    console.log(data)
    const svg = d3.select(`#${this.svgName}`)
    this.radarChart = new d3SideRadarChart(
      svg,
      data,
      { top: 0, right: 20, bottom: 0, left: 0 },
      this,
      this.props.isSideRadar
    )
    this.setState({ dataLoaded: true })

    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  }
}

export default RadarChart
