// react imports
import React, { Component } from 'react'

// component stylesheet
import '../styles/CountryViewContainer.scss'

// npm package imports
import * as d3 from 'd3'

// other react components
import CountrySelector from '../components/CountrySelector'
import RadarChart from '../components/RadarChart'
import LineChart from '../components/LineChart'
import IndicesTable from '../components/IndicesTable'

// data and utils
import dataFile from '../data/full_dataset.csv'
import summaryTextFile from '../data/countrySummary.csv'
import { getRealNameFromURL, getURLNameFromRealName } from '../utils/textUtils'

export class CountryViewContainer extends Component {
  constructor(props) {
    super(props)

    this.state = {
      data: null,
      summaries: null,
      selectedCountry: null,
      lineChartYear: null,
    }
  }

  handleCountryChange = (name, value) => {
    // this.setState({ selectedCountry: value });

    const urlName = getURLNameFromRealName(value)
    this.props.history.push(`/explore/${urlName}`)
  }

  handleLineScrub = (year) => {
    if (year !== this.state.lineChartYear)
      this.setState({ lineChartYear: year })
  }

  render() {
    console.log(this.props)

    const urlCountry = getRealNameFromURL(this.props.match.params.country)
    const renderCountry = urlCountry || 'United Kingdom'

    // empty divs for preload
    var radarComponent0 = <div></div>
    var radarComponent1 = <div></div>
    var radarComponent2 = <div></div>
    var radarComponent3 = <div></div>
    var lineChart = <div></div>
    var indicesTable = <div></div>
    var countrySummary = <div></div>

    if (this.state.data) {
      radarComponent0 = (
        <RadarChart
          svgName="svg-country-radar-0"
          metric="mean_overall"
          radarCountryA={renderCountry}
          data={this.state.data}
          isSideRadar={false}
        />
      )

      radarComponent1 = (
        <RadarChart
          svgName="svg-country-radar-1"
          metric="mean_inputs"
          radarCountryA={renderCountry}
          data={this.state.data}
          isSideRadar={false}
        />
      )

      radarComponent2 = (
        <RadarChart
          svgName="svg-country-radar-2"
          metric="mean_throughputs"
          radarCountryA={renderCountry}
          data={this.state.data}
          isSideRadar={false}
        />
      )

      radarComponent3 = (
        <RadarChart
          svgName="svg-country-radar-3"
          metric="mean_outputs"
          radarCountryA={renderCountry}
          data={this.state.data}
          isSideRadar={false}
        />
      )

      lineChart = (
        <LineChart
          svgName="svg-country-line-chart"
          selectedCountry={renderCountry}
          data={this.state.data}
          isSideRadar={false}
          handleLineScrub={this.handleLineScrub}
        />
      )

      indicesTable = (
        <IndicesTable
          data={this.state.data}
          selectedCountry={renderCountry}
          isSideRadar={false}
          year={this.state.lineChartYear}
        />
      )

      const selectedCountrySummary = this.state.summaries.find(
        (el) => el.Country === renderCountry
      ).Summary
      countrySummary = (
        <p className="country-summary">{selectedCountrySummary}</p>
      )
    }

    return (
      <div className="country-view-container">
        <div className="country-view-top-container">
          <div className="country-title-and-description-container">
            <h1 className="country-name">{renderCountry}</h1>
            {countrySummary}
          </div>
          <div className="country-selector-container">
            <CountrySelector
              label="View Another Country"
              value={renderCountry}
              name="country-selector"
              handleChange={this.handleCountryChange}
            />
          </div>
        </div>

        <div className="country-view-radar-container">
          <div className="radar-grid">
            <div className="radar-grid-row radar-grid-row1">
              <div className="radar-grid-box radar-box1">
                <h2>Overall Indices</h2>
                {radarComponent0}
              </div>
              <div className="radar-grid-box radar-box2">
                <h2>Quality of Democracy</h2>
                {radarComponent1}
              </div>
            </div>
            <div className="radar-grid-row radar-grid-row2">
              <div className="radar-grid-box radar-box3">
                <h2>Quality of Government</h2>
                {radarComponent2}
              </div>
              <div className="radar-grid-box radar-box4">
                <h2>Quality of Life</h2>
                {radarComponent3}
              </div>
            </div>
          </div>
        </div>

        <h2>{renderCountry} Index Changes Over Time</h2>
        <div className="country-line-and-table-container">
          {lineChart}
          {/* <h2>Detailed Data for {renderCountry}</h2> */}
          {indicesTable}
        </div>
      </div>
    )
  }

  componentDidMount = () => {
    Promise.all([d3.csv(dataFile), d3.csv(summaryTextFile)]).then((values) => {
      this.setState({
        data: values[0],
        summaries: values[1],
      })
    })
  }
}

export default CountryViewContainer
