import React from 'react';
import '../styles/SpanSortControl.scss';
import Select from 'react-select';
import { spanSortColors, dropdownStyles } from '../utils/constants';

function SpanSortControl(props) {
  const handleChange = e => {
    const clicked = e.value;
    props.clicked(clicked);
  };

  const selectOptions = [
    { value: 'mean_overall', label: 'Overall Score' },
    { value: 'country', label: 'Country Name' },
    { value: 'mean_inputs', label: 'Democracy Score' },
    { value: 'mean_throughputs', label: 'Government Score' },
    { value: 'mean_outputs', label: 'Life Score' },
  ];

  const customTheme = theme => {
    return {
      ...theme,
      colors: {
        ...theme.colors,
        primary: spanSortColors.dropdownPrimaryColor,
        primary25: spanSortColors.dropdownPrimaryColor25,
        primary50: spanSortColors.dropdownPrimaryColor50,
        primary75: spanSortColors.dropdownPrimaryColor75,
      },
    };
  };

  return (
    <div className="span-sort-container">
      <div className="span-sort-label-container">
        <span className="field-label">sort by</span>
      </div>
      <Select
        className="sort-selector"
        options={selectOptions}
        value={selectOptions.find(el => el.value === props.sortParameter)}
        onChange={handleChange}
        theme={customTheme}
        styles={dropdownStyles}
      />
    </div>
  );
}

export default SpanSortControl;
