import React, { Component } from 'react'
import * as d3 from 'd3'
import dataFile from '../data/full_dataset.csv'
import RadarChart from '../components/RadarChart'
import CountryTag from '../components/CountryTag'
import BlankCountryTag from '../components/BlankCountryTag'
import OverviewWelcome from '../components/OverviewWelcome'
import { getIndexTitleAndDescription } from '../utils/textUtils'
import '../styles/RadarSideBar.scss'

export class RadarSideBar extends Component {
  state = {
    data: null,
  }

  handleTagClearClicked = (country) => {
    this.props.handleTagClear(country)
  }

  render() {
    var tags = []
    var radarComponent = <div></div>

    const indexDescriptionInfo = getIndexTitleAndDescription(this.props.metric)

    if (this.state.data) {
      if (!this.props.metricIsSelected) {
        tags.push(<BlankCountryTag key={'A'} metric={this.props.metric} />)
        tags.push(<BlankCountryTag key={'B'} metric={this.props.metric} />)
      } else {
        if (this.props.metricIsSelected) {
          if (this.props.radarCountryA) {
            tags.push(
              <CountryTag
                data={this.state.data}
                metric={this.props.metric}
                key="A"
                legend="A"
                countryName={this.props.radarCountryA}
                handleTagClearClicked={this.handleTagClearClicked}
              ></CountryTag>
            )
          } else {
            tags.push(<BlankCountryTag key={'A'} metric={this.props.metric} />)
          }

          if (this.props.radarCountryB) {
            tags.push(
              <CountryTag
                data={this.state.data}
                metric={this.props.metric}
                key="B"
                legend="B"
                countryName={this.props.radarCountryB}
                handleTagClearClicked={this.handleTagClearClicked}
              ></CountryTag>
            )
          } else {
            tags.push(<BlankCountryTag key={'B'} metric={this.props.metric} />)
          }
        }
      }

      radarComponent = (
        <RadarChart
          svgName="svg-radarchart"
          metric={this.props.metric}
          radarCountryA={this.props.radarCountryA}
          radarCountryB={this.props.radarCountryB}
          data={this.state.data}
          isSideRadar={true}
        ></RadarChart>
      )
    }

    var barContents
    if (this.props.metric) {
      barContents = (
        <div className="radar-side-bar UI-panel">
          <div className="radar-header-container">
            <div className="radar-text-container">
              <h2>{indexDescriptionInfo.title}</h2>
              <p className="subtext-block">
                {indexDescriptionInfo.description}
              </p>
            </div>
            <div className="country-tag-container">{tags}</div>
          </div>
          <div className="radar-container">{radarComponent}</div>
        </div>
      )
    } else {
      barContents = (
        <div className="radar-side-bar UI-panel">
          <div className="radar-header-container">
            <div className="radar-text-container">
              <h2>{indexDescriptionInfo.title}</h2>
            </div>
            <div className="radar-welcome-container">
              <OverviewWelcome />
            </div>
          </div>
        </div>
      )
    }

    return <div className="radar-sidebar-container">{barContents}</div>
  }

  componentDidMount() {
    d3.csv(dataFile).then((data) => {
      console.log(data)
      this.setState({ data: data })
    })
  }
}

export default RadarSideBar
