import React from 'react';
import '../styles/IndicesTable.scss';
import { getIndexNameFromId } from '../utils/textUtils';

const indexMap = [
  {
    main_metric: 'mean_inputs',
    sub_metrics: [
      'mean_civ.soc',
      'mean_culture',
      'mean_feedback',
      'mean_democratic.engagement',
    ],
  },
  {
    main_metric: 'mean_throughputs',
    sub_metrics: [
      'mean_analytical',
      'mean_coordination',
      'mean_corruption',
      'mean_impartiality',
      'mean_judicial.impartiality',
      'mean_politicized.bureaucracy',
      'mean_procedural.efficiency',
      'mean_recruitment',
      'mean_regulatory.burden',
      'mean_tax.collection',
    ],
  },
  {
    main_metric: 'mean_outputs',
    sub_metrics: [
      'mean_civil.justice',
      'mean_crime',
      'mean_business.regulation',
      'mean_education',
      'mean_environment',
      'mean_inflation',
      'mean_other.public.services',
      'mean_price.levels',
      'mean_public.health',
      'mean_shadow.economy',
    ],
  },
];

function IndicesTable(props) {
  const YEAR = process.env.REACT_APP_YEAR;

  const selectedYear = props.year || YEAR;

  const tableData = props.data.find(
    el => el.country === props.selectedCountry && el.year === selectedYear
  );

  var tableDivs = [];
  indexMap.forEach(el => {
    var appendDiv = (
      <div className="table-main-index" key={el.main_metric}>
        <span className="table-index">
          {getIndexNameFromId(el.main_metric)}
        </span>
        <span className="table-value">{tableData[el.main_metric]}</span>
      </div>
    );

    tableDivs.push(appendDiv);

    el.sub_metrics.forEach(subEl => {
      const value = tableData[subEl] !== 'NA' ? tableData[subEl] : '-';

      appendDiv = (
        <div className="table-sub-index" key={subEl}>
          <span className="table-index">{getIndexNameFromId(subEl)}</span>
          <span className="table-value">{value}</span>
        </div>
      );

      tableDivs.push(appendDiv);
    });
  });

  return (
    <div className="indices-table">
      <div className="year-container">{`Year: ${props.year || YEAR}`}</div>
      {tableDivs}
    </div>
  );
}

export default IndicesTable;
