import React from 'react';
import '../styles/CountryTag.scss';

function BlankCountryTag(props) {
  let bottomText;
  switch (props.metric) {
    case null:
      bottomText = 'Choose a data point on the left to inspect';
      break;
    case 'mean_overall':
      bottomText =
        'Choose a data point on the left to compare the components of the Berggruen Index';
      break;
    case 'mean_inputs':
      bottomText =
        'Choose a data point on the left to compare sub-indices for Quality of Democracy';
      break;
    case 'mean_throughputs':
      bottomText =
        'Choose a data point on the left to compare sub-indices for Quality of Government';
      break;
    case 'mean_outputs':
      bottomText =
        'Choose a data point on the left to compare sub-indices for Quality of Life';
      break;
    default:
      break;
  }

  return (
    <div className="country-tag">
      <div className="tag-top-bar">
        <div className="blank-tag-title-container">Select a country</div>
      </div>
      <div className="tag-bottom-bar">
        <div className="blank-tag-description-container">{bottomText}</div>
      </div>
    </div>
  );
}

export default BlankCountryTag;
