import '../utils/constants';
import { lineChartColors } from '../utils/constants';
import * as d3 from 'd3';
import legend from 'd3-svg-legend';

export class d3LineChart {
  constructor(svg, data, margin) {
    const YEAR = process.env.REACT_APP_YEAR;

    this.data = data;
    this.svg = svg;
    this.margin = margin;

    this.width =
      Math.round(+svg.style('width').replace('px', '')) -
      this.margin.left -
      this.margin.right;
    this.height =
      Math.round(+svg.style('height').replace('px', '')) -
      this.margin.top -
      this.margin.bottom;

    this.startDate = new Date('2004');
    // this.endDate = new Date('2018')
    this.endDate = new Date(`${YEAR}/01/01`);

    this.xScale = d3
      .scaleTime()
      .domain([this.startDate, this.endDate])
      .range([0, this.width]);
    this.yScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([this.height, 0]);

    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale);

    this.colorScale = d3
      .scaleOrdinal()
      .domain(Object.keys(lineChartColors.legend))
      .range(Object.values(lineChartColors.legend));

    this.lineGenerator = d3
      .line()
      .x(d => this.xScale(d.year))
      .y(d => this.yScale(d.value))
      .curve(d3.curveMonotoneX);

    this.g = this.svg
      .append('g')
      .attr('transform', 'translate(' + margin.left + ', ' + margin.top + ')');
  }

  drawAxis() {
    this.xAxis = d3.axisBottom(this.xScale);

    var x = this.g.selectAll('.line-xaxis').data(['dummy']);
    x.enter()
      .append('g')
      .merge(x)
      .call(this.xAxis)
      .attr('class', 'line-xaxis')
      .attr('transform', 'translate(' + 0 + ', ' + this.height + ')');

    // grid lines
    this.g.selectAll('.line-horizontal-grid-line').remove();
    this.g
      .selectAll('.line-horizontal-grid-line')
      .data([20, 40, 60, 80, 100])
      .enter()
      .append('line')
      .attr('class', 'line-horizontal-grid-line line-grid-line')
      .attr('x1', this.width)
      .attr('y1', d => this.yScale(d))
      .attr('y2', 0)
      .attr('y2', d => this.yScale(d))
      .attr('stroke', lineChartColors.gridLineColor)
      .attr('stroke-width', 1)
      .lower();

    this.yAxis = d3.axisLeft(this.yScale);
    var y = this.g.selectAll('.line-yaxis').data(['dummy']);
    y.enter()
      .append('g')
      .merge(y)
      .call(this.yAxis)
      .attr('class', 'line-yaxis');

    // grid lines
    this.g.selectAll('.line-vertical-grid-line').remove();
    this.g
      .selectAll('.line-vertical-grid-line')
      .data(this.xScale.ticks())
      .enter()
      .append('line')
      .attr('class', 'line-vertical-grid-line line-grid-line')
      .attr('x1', d => this.xScale(d))
      .attr('y1', this.height)
      .attr('x2', d => this.xScale(d))
      .attr('y2', 0)
      .attr('stroke', lineChartColors.gridLineColor)
      .attr('stroke-width', 1)
      .lower();
  }

  resize(pixWidth, pixHeight) {
    this.width =
      Math.round(+pixWidth.replace('px', '')) -
      this.margin.left -
      this.margin.right;
    this.height =
      Math.round(+pixHeight.replace('px', '')) -
      this.margin.top -
      this.margin.bottom;
    this.xScale = d3
      .scaleTime()
      .domain([this.startDate, this.endDate])
      .range([0, this.width]);
    this.yScale = d3
      .scaleLinear()
      .domain([0, 100])
      .range([this.height, 0]);
    this.xAxis = d3.axisBottom(this.xScale);
    this.yAxis = d3.axisLeft(this.yScale);
  }

  getCountryData(country) {
    var retval = [];

    ['mean_overall', 'mean_throughputs', 'mean_outputs', 'mean_inputs'].forEach(
      metric => {
        const metricData = this.data
          .filter(el => el.country === country)
          .map(el => {
            return {
              year: new Date(el.year),
              metric: metric,
              value: +el[metric],
            };
          });

        retval = [...retval, ...metricData];
      }
    );

    return retval;
  }

  getDataPerMetric(countryData) {
    let retval = d3
      .nest()
      .key(d => d.metric)
      .entries(countryData);

    return retval;
  }

  render(props) {
    const { country, handleLineScrub, width, height } = props;

    if (width && height) {
      this.resize(width, height);
    }
    this.drawAxis();

    const countryData = this.getCountryData(country);
    var linePoints = this.g.selectAll('.line-point').data(countryData);

    linePoints
      .enter()
      .append('circle')
      .merge(linePoints)
      .attr('class', 'line-point')
      .attr('cx', d => this.xScale(d.year))
      .attr('cy', d => this.yScale(d.value))
      .attr('r', 4)
      .attr('fill', d => lineChartColors.lines[d.metric]);

    linePoints.exit().remove();

    const perMetricData = this.getDataPerMetric(countryData);

    var lines = this.g.selectAll('.timeseries-line').data(perMetricData);

    lines
      .enter()
      .append('path')
      .merge(lines)
      .attr('class', d => `timeseries-line timeseries-line-${d.key}`)
      .attr('fill', 'none')
      .attr('stroke-width', 2)
      .attr('stroke', d => lineChartColors.lines[d.key])
      .attr('d', d => this.lineGenerator(d.values));

    const lastOverallValue = countryData.find(
      el => el.year.getFullYear() === 2017 && el.metric === 'mean_overall'
    ).value;
    let legendTop = lastOverallValue < 50;

    let legendX = legendTop ? 20 : this.width - 200;
    const legendY = legendTop ? 20 : this.height - 75;
    legendX = country === 'Chile' ? 20 : legendX;

    // legend
    this.g.selectAll('.line-legend,.line-legend-background').remove();

    this.g
      .append('g')
      .attr('class', 'line-legend')
      .attr('transform', `translate(${legendX}, ${legendY})`);

    var colorLegend = legend
      .legendColor()
      .shape('circle')
      .scale(this.colorScale)
      .shapeRadius(5)
      .labelAlign('end');

    this.g.select('.line-legend').call(colorLegend);

    const coords = this.g
      .select('.line-legend')
      .node()
      .getBBox();

    this.g
      .append('rect')
      .attr('class', 'line-legend-background')
      .attr('transform', `translate(${legendX}, ${legendY})`)
      .attr('x', coords.x - 5)
      .attr('y', coords.y - 5)
      .attr('width', coords.width + 10)
      .attr('height', coords.height + 10)
      .attr('rx', 6)
      .attr('ry', 6)
      .attr('stroke', 'black')
      .attr('stroke-weight', 2)
      .attr('fill', 'white');

    this.g.select('.line-legend').raise();

    const handleMouseMove = dotThis => {
      const mouse = d3.mouse(dotThis);
      const numCountries = this.xScale.ticks().length;
      const bandSize = this.width / numCountries;
      const band = Math.floor((mouse[0] - this.margin.left) / bandSize);
      const lineX = this.xScale.ticks()[band];

      let scrubLine = this.g.selectAll('.scrub-line').data([lineX]);
      scrubLine
        .enter()
        .append('line')
        .merge(scrubLine)
        .attr('class', 'scrub-line')
        .attr('x1', d => this.xScale(d))
        .attr('y1', 0)
        .attr('x2', d => this.xScale(d))
        .attr('y2', this.height)
        .attr('stroke', lineChartColors.scrubLineColor)
        .attr('stroke-width', 1);

      try {
        handleLineScrub(`${lineX.getFullYear()}`);
      } catch (error) {}
    };

    this.svg.on('mousemove', function() {
      handleMouseMove(this);
    });
    this.svg.on('mouseout', () => {
      d3.selectAll('.scrub-line').remove();
      handleLineScrub(null);
    });
  }
}
