import React, { Component } from 'react'
import '../styles/CompareSelector.scss'
import Select from 'react-select'
import { getIndexNameFromId, getIndexIDsFromName } from '../utils/textUtils'
import { comparePageColors, dropdownStyles } from '../utils/constants'

export class CompareSelector extends Component {
  handleChange = (e) => {
    this.props.handleChange(this.props.id, e.value)
  }

  render() {
    const groupedOptions = [
      {
        label: 'Main Indices',
        options: [
          { label: getIndexNameFromId('mean_inputs'), value: 'mean_inputs' },
          {
            label: getIndexNameFromId('mean_throughputs'),
            value: 'mean_throughputs',
          },
          { label: getIndexNameFromId('mean_outputs'), value: 'mean_outputs' },
        ],
      },
      {
        label: getIndexNameFromId('mean_inputs') + ' Sub-Indices',
        options: [
          { label: getIndexNameFromId('mean_civ.soc'), value: 'mean_civ.soc' },
          { label: getIndexNameFromId('mean_culture'), value: 'mean_culture' },
          {
            label: getIndexNameFromId('mean_feedback'),
            value: 'mean_feedback',
          },
          {
            label: getIndexNameFromId('mean_democratic.engagement'),
            value: 'mean_democratic.engagement',
          },
        ],
      },
      {
        label: getIndexNameFromId('mean_throughputs') + ' Sub-Indices',
        options: [
          {
            label: getIndexNameFromId('mean_analytical'),
            value: 'mean_analytical',
          },
          {
            label: getIndexNameFromId('mean_coordination'),
            value: 'mean_coordination',
          },
          {
            label: getIndexNameFromId('mean_corruption'),
            value: 'mean_corruption',
          },
          {
            label: getIndexNameFromId('mean_impartiality'),
            value: 'mean_impartiality',
          },
          {
            label: getIndexNameFromId('mean_judicial.impartiality'),
            value: 'mean_judicial.impartiality',
          },
          {
            label: getIndexNameFromId('mean_politicized.bureaucracy'),
            value: 'mean_politicized.bureaucracy',
          },
          {
            label: getIndexNameFromId('mean_procedural.efficiency'),
            value: 'mean_procedural.efficiency',
          },
          {
            label: getIndexNameFromId('mean_recruitment'),
            value: 'mean_recruitment',
          },
          {
            label: getIndexNameFromId('mean_regulatory.burden'),
            value: 'mean_regulatory.burden',
          },
          {
            label: getIndexNameFromId('mean_tax.collection'),
            value: 'mean_tax.collection',
          },
          {
            label: getIndexNameFromId('mean_government.transparency'),
            value: 'mean_government.transparency',
          },
        ],
      },
      {
        label: getIndexNameFromId('mean_outputs') + ' Sub-Indices',
        options: [
          {
            label: getIndexNameFromId('mean_civil.justice'),
            value: 'mean_civil.justice',
          },
          { label: getIndexNameFromId('mean_crime'), value: 'mean_crime' },
          {
            label: getIndexNameFromId('mean_business.regulation'),
            value: 'mean_business.regulation',
          },
          {
            label: getIndexNameFromId('mean_education'),
            value: 'mean_education',
          },
          {
            label: getIndexNameFromId('mean_environment'),
            value: 'mean_environment',
          },
          {
            label: getIndexNameFromId('mean_inflation'),
            value: 'mean_inflation',
          },
          {
            label: getIndexNameFromId('mean_other.public.services'),
            value: 'mean_other.public.services',
          },
          {
            label: getIndexNameFromId('mean_price.levels'),
            value: 'mean_price.levels',
          },
          {
            label: getIndexNameFromId('mean_public.health'),
            value: 'mean_public.health',
          },
          {
            label: getIndexNameFromId('mean_shadow.economy'),
            value: 'mean_shadow.economy',
          },
        ],
      },
    ]

    let label
    if (this.props.id === 'compare-selector-x') {
      label = 'first index score (x-axis)'
    } else {
      label = 'second index score (y-axis)'
    }

    const groupStyles = {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      fontStyle: 'italic',
      color: 'gray',
      textTransform: 'capitalize',
      // borderTop: '1px solid gray',
      paddingTop: '5px',
    }

    const customStyles = {
      option: (provided, state) => ({
        ...provided,
        // borderBottom: '1px dotted pink',
        // color: state.isSelected ? 'red' : 'blue',
        // padding: 20,
      }),
    }

    const customTheme = (theme) => {
      return {
        ...theme,
        colors: {
          ...theme.colors,
          primary: comparePageColors.dropdownPrimaryColor,
          primary25: comparePageColors.dropdownPrimaryColor25,
          primary50: comparePageColors.dropdownPrimaryColor50,
          primary75: comparePageColors.dropdownPrimaryColor75,
        },
      }
    }

    const formatGroupLabel = (data) => (
      <div style={groupStyles}>
        <span>{data.label}</span>
      </div>
    )

    const propsOption = {
      label: getIndexNameFromId(this.props.value),
      value: this.props.value,
    }

    return (
      <div className="compare-selector-container">
        <div className="field-label">{label}</div>
        <Select
          className="compare-selector"
          options={groupedOptions}
          value={propsOption}
          onChange={this.handleChange}
          formatGroupLabel={formatGroupLabel}
          styles={dropdownStyles}
          theme={customTheme}
        />
      </div>
    )
  }
}

export default CompareSelector
