import React, { Component } from 'react';
import '../styles/LineChart.scss';
import '../charts/d3LineChart';
import { d3LineChart } from '../charts/d3LineChart';
import * as d3 from 'd3';

export class LineChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      width: null,
      height: null,
      dataLoaded: false,
    };

    this.svgName = props.svgName;
    this.lineChart = null;
  }

  shouldComponentUpdate(nextProps, nextState) {
    this.lineChart.render({
      country: nextProps.selectedCountry,
      width: nextState.width,
      height: nextState.height,
      handleLineScrub: this.props.handleLineScrub,
    });

    return false;
  }

  resizeHandler = () => {
    const cont = d3.select('.line-chart-container');

    if (!cont.empty()) {
      this.setState({
        width: cont.style('width'),
        height: cont.style('height'),
      });
    }
  };

  render() {
    return (
      <div className="line-chart-container">
        <svg id={this.svgName}></svg>
      </div>
    );
  }

  componentDidMount() {
    const svg = d3.select(`#${this.svgName}`);
    this.lineChart = new d3LineChart(svg, this.props.data, {
      top: 20,
      right: 30,
      bottom: 30,
      left: 40,
    });
    this.setState({ dataLoaded: true });

    window.addEventListener('resize', this.resizeHandler);
  }
}

export default LineChart;
