import React, { Component } from 'react';
import '../styles/CountryTag.scss';
import { getURLNameFromRealName } from '../utils/textUtils';
import { Link } from 'react-router-dom';

import { ReactComponent as KeyAOverall } from '../images/radarKeys/keyA.svg';
import { ReactComponent as KeyBOverall } from '../images/radarKeys/keyB.svg';
import { ReactComponent as KeyADem } from '../images/radarKeys/key-first-democracy.svg';
import { ReactComponent as KeyBDem } from '../images/radarKeys/key-second-democracy.svg';
import { ReactComponent as KeyAGov } from '../images/radarKeys/key-first-government.svg';
import { ReactComponent as KeyBGov } from '../images/radarKeys/key-second-government.svg';
import { ReactComponent as KeyALife } from '../images/radarKeys/key-first-life.svg';
import { ReactComponent as KeyBLife } from '../images/radarKeys/key-second-life.svg';

export class CountryTag extends Component {
  handleClearClicked = () => {
    this.props.handleTagClearClicked(this.props.countryName);
  };

  getScores = country => {
    const YEAR = process.env.REACT_APP_YEAR;

    const dataRow = this.props.data.find(
      el => el.country === this.props.countryName && el.year === YEAR
    );
    return {
      overall: dataRow['mean_overall'],
      democracy: dataRow['mean_inputs'],
      government: dataRow['mean_throughputs'],
      life: dataRow['mean_outputs'],
    };
  };

  isSelectedMetric = metric => {
    return metric === this.props.metric ? 'score-selected' : '';
  };

  render() {
    let legend;

    switch (this.props.metric) {
      case 'mean_overall':
        legend =
          this.props.legend == 'A' ? (
            <KeyAOverall className="tag-key" />
          ) : (
            <KeyBOverall className="tag-key" />
          );
        break;
      case 'mean_inputs':
        legend =
          this.props.legend == 'A' ? (
            <KeyADem className="tag-key" />
          ) : (
            <KeyBDem className="tag-key" />
          );
        break;
      case 'mean_throughputs':
        legend =
          this.props.legend == 'A' ? (
            <KeyAGov className="tag-key" />
          ) : (
            <KeyBGov className="tag-key" />
          );
        break;
      case 'mean_outputs':
        legend =
          this.props.legend == 'A' ? (
            <KeyALife className="tag-key" />
          ) : (
            <KeyBLife className="tag-key" />
          );
        break;
      default:
        legend =
          this.props.legend == 'A' ? (
            <KeyAOverall className="tag-key" />
          ) : (
            <KeyBOverall className="tag-key" />
          );
        break;
    }

    const scoreValues = this.getScores(this.props.countryName);
    return (
      <div className="country-tag">
        {/* {legend} */}
        <div className="tag-top-bar">
          <span className="country-tag-name noselect">
            {this.props.countryName}
          </span>
          <div className="tag-top-bar-controls-container">
            <div className="tag-details-button noselect">
              <Link
                to={`${''}/explore/${getURLNameFromRealName(
                  this.props.countryName
                )}`}
              >
                Details
              </Link>
            </div>

            <div
              className="tag-clear-button noselect"
              onClick={this.handleClearClicked}
            >
              Hide
            </div>
          </div>
        </div>
        <div className="tag-bottom-bar">
          <div className="tag-bottom-bar-left">{legend}</div>
          <div className="tag-bottom-bar-right">
            <div
              className={`tag-score-details-row ${this.isSelectedMetric(
                'mean_overall'
              )}`}
            >
              <div className="tag-score-details-row-label">Overall</div>
              <div className="tag-score-details-row-value">
                {scoreValues.overall}/100
              </div>
            </div>
            <div
              className={`tag-score-details-row ${this.isSelectedMetric(
                'mean_inputs'
              )}`}
            >
              <div className="tag-score-details-row-label">Democracy</div>
              <div className="tag-score-details-row-value">
                {scoreValues.democracy}/100
              </div>
            </div>
            <div
              className={`tag-score-details-row ${this.isSelectedMetric(
                'mean_throughputs'
              )}`}
            >
              <div className="tag-score-details-row-label">Government</div>
              <div className="tag-score-details-row-value">
                {scoreValues.government}/100
              </div>
            </div>
            <div
              className={`tag-score-details-row ${this.isSelectedMetric(
                'mean_outputs'
              )}`}
            >
              <div className="tag-score-details-row-label">Life</div>
              <div className="tag-score-details-row-value">
                {scoreValues.life}/100
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CountryTag;
