import React from 'react';
import '../styles/App.scss';
import NavBar from './NavBar';
import OverviewChartContainer from './OverviewChartContainer';
import CompareChartContainer from './CompareChartContainer';
import CountryViewContainer from './CountryViewContainer';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import * as d3 from 'd3';

function App(props) {
  const urlPath = props.location.pathname.split('/')[1];
  let pageNumber;

  switch (urlPath) {
    case '':
      pageNumber = 0;
      break;
    case 'explore':
      pageNumber = 2;
      break;
    case 'compare':
      pageNumber = 1;
      break;
    default:
      pageNumber = 0;
      break;
  }

  const handleChange = newValue => {};

  return (
    <div className="App">
      <NavBar handleNavClick={handleChange} selectedValue={pageNumber} />
      <div className="app-content-container">
        <Switch>
          <Route path={`/`} exact component={OverviewChartContainer} />
          <Route path={`/compare/:x?/:y?`} component={CompareChartContainer} />
          <Route path={`/explore/:country?`} component={CountryViewContainer} />
        </Switch>
      </div>
    </div>
  );
}

export default App;
