import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../styles/NavBar.scss';
import { nav_slider, setNav } from '../utils/navUtils';
import * as d3 from 'd3';

import { ReactComponent as LogoText } from '../images/logo.svg';
import { ReactComponent as LogoGraphic } from '../images/logo-graphic.svg';

function NavBar(props) {
  const items = [
    '#overview-nav-link',
    '#compare-nav-link',
    '#explore-nav-link',
  ];

  const handleClick = e => {
    const clicked = e.target.getAttribute('id');

    switch (clicked) {
      case 'overview-nav-link':
        props.handleNavClick(0);
        break;
      case 'compare-nav-link':
        props.handleNavClick(1);
        break;
      case 'explore-nav-link':
        props.handleNavClick(2);
        break;
      default:
        break;
    }
  };

  const handleBClick = () => {
    window.open(
      'https://www.berggruen.org/work/the-future-of-democracy/the-2019-berggruen-governance-index/'
    );
  };

  React.useEffect(() => {
    //clear selected class from all links
    items.forEach(item => {
      d3.select(item).classed('nav-is-selected', false);
    });

    // now add to selected one
    d3.select(items[props.selectedValue]).classed('nav-is-selected', true);

    setNav(props.selectedValue);
  }, [props.selectedValue]);

  return (
    <div className="nav-bar-container" id="nav-bar-container">
      <div className="branded-area-container">
        <div className="brand-logo-box">
          <LogoText onClick={handleBClick} />
        </div>
        <div className="brand-text-box">
          <h1 className="brand-app-title">
            The 2019 Berggruen Governance Index
          </h1>
        </div>
        <div className="nav-links-container">
          <ul id="nav">
            <li className="nav-link-li">
              <Link to={`/`}>
                <div
                  className="nav-link"
                  id="overview-nav-link"
                  onClick={handleClick}
                >
                  Overview
                </div>
              </Link>
            </li>
            <li className="nav-link-li">
              <Link to={`/compare`}>
                <div
                  className="nav-link"
                  id="compare-nav-link"
                  onClick={handleClick}
                >
                  Compare
                </div>
              </Link>
            </li>
            <li className="nav-link-li">
              <Link to={`/explore`}>
                <div
                  className="nav-link"
                  id="explore-nav-link"
                  onClick={handleClick}
                >
                  Explore
                </div>
              </Link>
            </li>
            <div className="nav-slider" id="nav-slide-click"></div>
          </ul>
        </div>
      </div>
      <div className="right-branded-area-container">
        <LogoGraphic onClick={handleBClick} />
      </div>
    </div>
  );
}

export default NavBar;
