import * as d3 from 'd3';
import React, { Component } from 'react';
import dataFile from '../data/full_dataset.csv';
import { nestData } from '../utils/dataUtils';
import { d3SpanChart } from '../charts/d3SpanChart';
import { getURLNameFromRealName } from '../utils/textUtils';
import '../styles/SpanChart.scss';

export class SpanChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      metrics: props.metrics,
      hovered: null,
      width: null,
      height: null,
    };

    this.spanChart = '';
  }

  shouldComponentUpdate = (nextProps, nextState) => {
    const { metrics, activeMetric, radarCountryA, radarCountryB } = nextProps;

    var selectedCountries = [radarCountryA, radarCountryB].filter(
      el => el !== null
    );

    var toResizeFlag;
    if (!nextState.height || !nextState.width) {
      toResizeFlag = false;
    } else {
      toResizeFlag = true;
    }

    const cont = d3.select('.span-chart-container');
    this.spanChart.render({
      metrics: metrics,
      activeMetric: activeMetric,
      selectedCountries: selectedCountries,
      metricIsSelected: nextProps.metricIsSelected,
      sortOrder: nextProps.sortValue,
      hovered: nextState.hovered,
      toResize: false,
      width: cont.style('width'),
      height: cont.style('height'),
    });

    return false;
  };

  dotHoverHandler = (country, metric) => {
    // this.setState({ activeMetric: metric })
    this.props.handleSpanDotHover(country, metric);

  d3.selectAll(`.marker-${this.props.activeMetric}`).raise();

    if (
      country &&
      (!this.props.activeMetric || this.props.activeMetric === metric) &&
      (!this.props.radarCountryA || !this.props.radarCountryB)
    ) {
      d3.selectAll(
        `.hover-circle-${getURLNameFromRealName(country)}-${metric}`
      ).style('opacity', 1);
    }
  };

  dotResetHover = () => {
    this.props.handleSpanHoverReset();

    d3.selectAll(`.hover-circle`).style('opacity', 0);
    // this.setState({ hovered: null })
  };

  dotClickedHandler = (country, metric) => {
    this.props.handleSpanClick(country, metric);
  };

  resizeHandler = () => {
    const cont = d3.select('.span-chart-container');
    if (!cont.empty()) {
      this.spanChart.render({
        metrics: this.props.metrics,
        activeMetric: this.props.activeMetric,
        selectedCountries: this.props.selectedCountries,
        metricIsSelected: this.props.metricIsSelected,
        sortOrder: this.props.sortValue,
        toResize: true,
        width: cont.style('width'),
        height: cont.style('height'),
      });
      // this.setState({ width: cont.style('width'), height: cont.style('height') });
    }
  };

  render() {
    return (
      <div className="span-chart-container">
        <svg id={this.props.svgName} className="spanchart"></svg>
      </div>
    );
  }

  componentDidMount = () => {
    const cont = d3.select('.span-chart-container');

    d3.csv(dataFile).then(data => {
      data = nestData(data);
      const svg = d3.select(`#${this.props.svgName}`);
      this.spanChart = new d3SpanChart(
        svg,
        data,
        { top: 5, right: 30, bottom: 40, left: 120 },
        this
      );
      this.spanChart.render({
        metrics: this.state.metrics,
        activeMetric: null,
        selectedCountries: [],
        metricIsSelected: false,
        toResize: false,
        sortOrder: this.props.sortValue,
        width: cont.style('width'),
        height: cont.style('height'),
      });
    });

    window.addEventListener('resize', this.resizeHandler);
  };
}

export default SpanChart;
